import { useState } from 'react';

function Counter() {
  const [count, setCount] = useState(0);
  return (
    <div style={{ textAlign: 'center', padding: '20px' }}>
      <h3>Count: {count}</h3>
      <button onClick={() => setCount(count + 1)}>+</button>
      <button onClick={() => setCount(count - 1)} style={{ margin: '0 10px' }}>-</button>
      <button onClick={() => setCount(0)}>Reset</button>
    </div>
  );
}

function FormExample() {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');

  return (
    <div style={{ padding: '20px' }}>
      <input
        type="text"
        placeholder="Tn"
        value={name}
        onChange={(e) => setName(e.target.value)}
        style={{ margin: '5px', padding: '8px' }}
      />
      <input
        type="email"
        placeholder="Email"
        value={email}
        onChange={(e) => setEmail(e.target.value)}
        style={{ margin: '5px', padding: '8px' }}
      />
      <p>Tn: {name}</p>
      <p>Email: {email}</p>
    </div>
  );
}

function State() {
  const [showCounter, setShowCounter] = useState(true);

  return (
    <div className="topic-section">
      <h2>State vi useState Hook</h2>
      
      <div className="definition-box">
        <h4>nh ngha</h4>
        <p>
          <strong>State</strong> l d liu c th thay i trong component. 
          Khi state thay i, React s t ng re-render component  phn nh 
          s thay i . <span className="highlight">useState</span> l mt React Hook 
          cho php bn thm state vo functional components.
        </p>
      </div>

      <h3>C php useState</h3>
      <div className="code-example">
        <pre>{`import { useState } from 'react';

function Component() {
  const [state, setState] = useState(initialValue);
  
  return (
    <div>
      <p>{state}</p>
      <button onClick={() => setState(newValue)}>Update</button>
    </div>
  );
}`}</pre>
      </div>

      <div className="info-box">
        <strong>Gii thch:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li><span className="highlight">useState</span> tr v mt mng gm 2 phn t</li>
          <li>Phn t u tin l gi tr state hin ti</li>
          <li>Phn t th hai l hm  cp nht state</li>
          <li>Tham s ca useState l gi tr khi to</li>
        </ul>
      </div>

      <h3>V d c bn</h3>
      <div className="code-example">
        <pre>{`function Counter() {
  const [count, setCount] = useState(0);
  
  return (
    <div>
      <p>Count: {count}</p>
      <button onClick={() => setCount(count + 1)}>
        Increment
      </button>
      <button onClick={() => setCount(count - 1)}>
        Decrement
      </button>
    </div>
  );
}`}</pre>
      </div>

      <div className="interactive-demo">
        <h4>V d tng tc: Counter</h4>
        <Counter />
      </div>

      <h3>State vi nhiu gi tr</h3>
      <p>
        Bn c th s dng nhiu useState hooks trong mt component  qun l 
        nhiu state c lp.
      </p>

      <div className="code-example">
        <pre>{`function Form() {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [age, setAge] = useState(0);
  
  return (
    <form>
      <input 
        value={name} 
        onChange={(e) => setName(e.target.value)} 
      />
      <input 
        value={email} 
        onChange={(e) => setEmail(e.target.value)} 
      />
      <input 
        type="number"
        value={age} 
        onChange={(e) => setAge(Number(e.target.value))} 
      />
    </form>
  );
}`}</pre>
      </div>

      <div className="interactive-demo">
        <h4>V d tng tc: Form vi nhiu state</h4>
        <FormExample />
      </div>

      <h3>Cp nht State da trn State trc </h3>
      <p>
        Khi cp nht state da trn gi tr state hin ti, bn nn s dng 
        functional update  m bo tnh chnh xc.
      </p>

      <div className="code-example">
        <pre>{`//  C th khng chnh xc nu c nhiu updates
function Counter() {
  const [count, setCount] = useState(0);
  
  const increment = () => {
    setCount(count + 1); // Da vo gi tr hin ti
    setCount(count + 1); // Vn da vo gi tr c, khng phi gi tr mi
  };
}

//  NG - S dng functional update
function Counter() {
  const [count, setCount] = useState(0);
  
  const increment = () => {
    setCount(prevCount => prevCount + 1);
    setCount(prevCount => prevCount + 1); // Da vo gi tr mi nht
  };
}`}</pre>
      </div>

      <h3>State vi Objects v Arrays</h3>
      <p>
        Khi state l object hoc array, bn phi to mt object/array mi 
        thay v mutate (thay i trc tip) object/array c.
      </p>

      <div className="code-example">
        <pre>{`function UserProfile() {
  const [user, setUser] = useState({ name: '', age: 0 });
  
  //  SAI - Mutate trc tip
  const updateNameWrong = (newName) => {
    user.name = newName; //  Khng hot ng!
    setUser(user);
  };
  
  //  NG - To object mi
  const updateName = (newName) => {
    setUser({ ...user, name: newName });
  };
  
  //  NG - S dng functional update
  const updateAge = (newAge) => {
    setUser(prevUser => ({ ...prevUser, age: newAge }));
  };
  
  return (
    <div>
      <p>{user.name} - {user.age}</p>
      <button onClick={() => updateName('John')}>Set Name</button>
      <button onClick={() => updateAge(25)}>Set Age</button>
    </div>
  );
}`}</pre>
      </div>

      <div className="code-example">
        <pre>{`function TodoList() {
  const [todos, setTodos] = useState([]);
  
  //  Thm item mi
  const addTodo = (text) => {
    setTodos([...todos, { id: Date.now(), text }]);
  };
  
  //  Xa item
  const removeTodo = (id) => {
    setTodos(todos.filter(todo => todo.id !== id));
  };
  
  //  Cp nht item
  const updateTodo = (id, newText) => {
    setTodos(todos.map(todo => 
      todo.id === id ? { ...todo, text: newText } : todo
    ));
  };
  
  return (
    <div>
      {todos.map(todo => (
        <div key={todo.id}>{todo.text}</div>
      ))}
    </div>
  );
}`}</pre>
      </div>

      <h3>Lazy Initial State</h3>
      <p>
        Nu gi tr khi to ca state cn tnh ton phc tp, bn c th truyn 
        mt function vo useState. Function ny ch chy mt ln khi component mount.
      </p>

      <div className="code-example">
        <pre>{`//  Tnh ton mi ln render
function ExpensiveComponent() {
  const [data, setData] = useState(expensiveCalculation());
  // expensiveCalculation() chy mi ln render
}

//  Ch tnh ton mt ln
function ExpensiveComponent() {
  const [data, setData] = useState(() => expensiveCalculation());
  // expensiveCalculation() ch chy mt ln khi mount
}`}</pre>
      </div>

      <h3>State v Re-rendering</h3>
      <div className="warning-box">
        <strong>Quan trng:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li>Khi state thay i, component s re-render</li>
          <li>Ch component c state v cc component con s re-render</li>
          <li>React s dng Object.is()  so snh state c v mi</li>
          <li>Nu gi tr state khng thay i (theo Object.is), component s khng re-render</li>
        </ul>
      </div>

      <div className="code-example">
        <pre>{`function Component() {
  const [count, setCount] = useState(0);
  
  // Re-render khi count thay i
  const increment = () => setCount(count + 1);
  
  // Khng re-render v gi tr khng i
  const noChange = () => setCount(0); // count  l 0
  
  return <div>{count}</div>;
}`}</pre>
      </div>

      <div className="info-box">
        <strong>Best Practices:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li>t state  component thp nht c th (lift state up khi cn)</li>
          <li>S dng functional update khi cp nht da trn state c</li>
          <li>Khng mutate state trc tip, lun to gi tr mi</li>
          <li>S dng lazy initial state cho tnh ton phc tp</li>
          <li>Trnh qu nhiu state, c th gp thnh object nu lin quan</li>
        </ul>
      </div>
    </div>
  );
}

export default State;

