function RESTAPIBasics() {
  return (
    <div className="topic-section">
      <h2>REST API Basics</h2>
      
      <div className="definition-box">
        <h4>nh ngha</h4>
        <p>
          <strong>REST (Representational State Transfer)</strong> l mt kin trc web service 
          s dng cc phng thc HTTP chun  thao tc vi ti nguyn. Cc phng thc chnh 
          bao gm <span className="highlight">GET</span>, <span className="highlight">POST</span>, 
          <span className="highlight">PUT</span>, v <span className="highlight">DELETE</span>.
        </p>
      </div>

      <h3>Cc phng thc HTTP c bn</h3>

      <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '20px', margin: '20px 0' }}>
        <div style={{ background: '#e8f5e9', padding: '15px', borderRadius: '8px' }}>
          <h4 style={{ color: '#2e7d32', marginBottom: '10px' }}>GET</h4>
          <p style={{ margin: 0, lineHeight: '1.6' }}>
            Ly d liu t server. Khng thay i d liu, ch c.
          </p>
        </div>

        <div style={{ background: '#fff3e0', padding: '15px', borderRadius: '8px' }}>
          <h4 style={{ color: '#e65100', marginBottom: '10px' }}>POST</h4>
          <p style={{ margin: 0, lineHeight: '1.6' }}>
            To ti nguyn mi trn server. Gi d liu trong request body.
          </p>
        </div>

        <div style={{ background: '#e3f2fd', padding: '15px', borderRadius: '8px' }}>
          <h4 style={{ color: '#1976d2', marginBottom: '10px' }}>PUT</h4>
          <p style={{ margin: 0, lineHeight: '1.6' }}>
            Cp nht ton b ti nguyn. Thay th d liu hin c.
          </p>
        </div>

        <div style={{ background: '#ffebee', padding: '15px', borderRadius: '8px' }}>
          <h4 style={{ color: '#c62828', marginBottom: '10px' }}>DELETE</h4>
          <p style={{ margin: 0, lineHeight: '1.6' }}>
            Xa ti nguyn khi server.
          </p>
        </div>
      </div>

      <h3>V d s dng</h3>

      <div className="code-example">
        <pre>{`// GET - Ly danh sch users
fetch('https://api.example.com/users')
  .then(response => response.json())
  .then(data => console.log(data));

// POST - To user mi
fetch('https://api.example.com/users', {
  method: 'POST',
  headers: {
    'Content-Type': 'application/json',
  },
  body: JSON.stringify({
    name: 'John Doe',
    email: 'john@example.com'
  })
})
  .then(response => response.json())
  .then(data => console.log(data));

// PUT - Cp nht user
fetch('https://api.example.com/users/123', {
  method: 'PUT',
  headers: {
    'Content-Type': 'application/json',
  },
  body: JSON.stringify({
    name: 'Jane Doe',
    email: 'jane@example.com'
  })
})
  .then(response => response.json())
  .then(data => console.log(data));

// DELETE - Xa user
fetch('https://api.example.com/users/123', {
  method: 'DELETE'
})
  .then(response => {
    if (response.ok) {
      console.log('User  c xa');
    }
  });`}</pre>
      </div>

      <h3>HTTP Status Codes</h3>
      <p>
        Server tr v status code  bo hiu kt qu ca request:
      </p>

      <div className="code-example">
        <pre>{`// 2xx - Success
200 OK - Request thnh cng
201 Created - Ti nguyn  c to
204 No Content - Thnh cng nhng khng c content tr v

// 4xx - Client Error
400 Bad Request - Request khng hp l
401 Unauthorized - Cn authentication
404 Not Found - Khng tm thy ti nguyn

// 5xx - Server Error
500 Internal Server Error - Li server
503 Service Unavailable - Service khng kh dng`}</pre>
      </div>

      <h3>RESTful URL Patterns</h3>
      <p>
        REST API thng s dng URL patterns nht qun:
      </p>

      <div className="code-example">
        <pre>{`// Collection resources
GET    /api/users          // Ly tt c users
POST   /api/users          // To user mi

// Individual resources
GET    /api/users/123       // Ly user c id 123
PUT    /api/users/123       // Cp nht user 123
DELETE /api/users/123       // Xa user 123

// Nested resources
GET    /api/users/123/posts // Ly posts ca user 123`}</pre>
      </div>

      <div className="info-box">
        <strong>Best Practices:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li>S dng danh t s nhiu cho collection endpoints (/users, /posts)</li>
          <li>Lun s dng HTTPS trong production</li>
          <li>X l errors mt cch nht qun</li>
          <li>S dng proper HTTP status codes</li>
          <li>API responses nn c cu trc nht qun</li>
        </ul>
      </div>

      <div className="warning-box">
        <strong>Lu  quan trng:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li>GET requests khng nn c side effects (khng thay i d liu)</li>
          <li>POST v PUT cn gi Content-Type header</li>
          <li>DELETE requests thng khng cn body</li>
          <li>Lun validate d liu trc khi gi request</li>
        </ul>
      </div>
    </div>
  );
}

export default RESTAPIBasics;

