import { useState } from 'react';

// Functional Component example
function WelcomeMessage({ name }) {
  return <h2>Cho mng, {name}!</h2>;
}

// Class Component example (commented out, but shown for reference)
/*
class WelcomeClass extends React.Component {
  render() {
    return <h2>Cho mng, {this.props.name}!</h2>;
  }
}
*/

function Components() {
  const [name, setName] = useState('Ngi dng');

  return (
    <div className="topic-section">
      <h2>Components</h2>
      
      <div className="definition-box">
        <h4>nh ngha</h4>
        <p>
          <strong>Component</strong> l cc khi xy dng c bn ca ng dng React. 
          Mt component l mt hm hoc class JavaScript tr v JSX  m t giao din 
          ngi dng. Components cho php bn chia nh UI thnh cc phn c lp, 
          c th ti s dng.
        </p>
      </div>

      <h3>Functional Components (Khuyn ngh)</h3>
      <p>
        Functional components l cc hm JavaScript n gin nhn props lm tham s 
        v tr v JSX. y l cch vit component c khuyn ngh trong React hin i.
      </p>

      <div className="code-example">
        <pre>{`// Functional Component
function Welcome({ name }) {
  return <h1>Hello, {name}!</h1>;
}

// Arrow Function Component
const Welcome = ({ name }) => {
  return <h1>Hello, {name}!</h1>;
};

// S dng component
<Welcome name="React" />`}</pre>
      </div>

      <div className="interactive-demo">
        <h4>V d Functional Component</h4>
        <input
          type="text"
          value={name}
          onChange={(e) => setName(e.target.value)}
          placeholder="Nhp tn ca bn"
          style={{
            padding: '8px',
            marginRight: '10px',
            borderRadius: '5px',
            border: '1px solid #667eea'
          }}
        />
        <WelcomeMessage name={name} />
      </div>

      <h3>Class Components (Legacy)</h3>
      <p>
        Class components l cch vit component c s dng ES6 classes. 
        Mc d vn c h tr, nhng React khuyn ngh s dng functional components 
        vi hooks thay v class components.
      </p>

      <div className="code-example">
        <pre>{`// Class Component
class Welcome extends React.Component {
  render() {
    return <h1>Hello, {this.props.name}!</h1>;
  }
}

// S dng component
<Welcome name="React" />`}</pre>
      </div>

      <h3>So snh Functional vs Class Components</h3>
      
      <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '20px', margin: '20px 0' }}>
        <div style={{ background: '#e8f5e9', padding: '15px', borderRadius: '8px' }}>
          <h4 style={{ color: '#2e7d32', marginBottom: '10px' }}>Functional Components</h4>
          <ul style={{ paddingLeft: '20px', lineHeight: '1.8' }}>
            <li>n gin hn, d c</li>
            <li>t code hn</li>
            <li>S dng Hooks cho state v lifecycle</li>
            <li>Hiu sut tt hn (React c th ti u ha tt hn)</li>
            <li>Khuyn ngh s dng</li>
          </ul>
        </div>

        <div style={{ background: '#fff3e0', padding: '15px', borderRadius: '8px' }}>
          <h4 style={{ color: '#e65100', marginBottom: '10px' }}>Class Components</h4>
          <ul style={{ paddingLeft: '20px', lineHeight: '1.8' }}>
            <li>Phc tp hn, nhiu boilerplate</li>
            <li>S dng this v bind</li>
            <li>C lifecycle methods</li>
            <li>Kh ti u ha hn</li>
            <li>Legacy, khng khuyn ngh</li>
          </ul>
        </div>
      </div>

      <h3>t tn Component</h3>
      <p>
        Tn component phi bt u bng ch ci in hoa. iu ny gip React phn bit 
        component vi cc th HTML thng thng.
      </p>

      <div className="code-example">
        <pre>{`//  NG - Component vi ch ci in hoa
function MyComponent() {
  return <div>Content</div>;
}

//  SAI - Component vi ch ci thng
function myComponent() {
  return <div>Content</div>;
}

// React s coi myComponent l th HTML thng thng
<myComponent /> //  Khng hot ng`}</pre>
      </div>

      <h3>Composing Components</h3>
      <p>
        Components c th c s dng bn trong cc components khc, cho php bn 
        xy dng UI phc tp t cc phn n gin.
      </p>

      <div className="code-example">
        <pre>{`function Header() {
  return <header><h1>My App</h1></header>;
}

function Content() {
  return <main><p>Main content</p></main>;
}

function Footer() {
  return <footer><p>Footer</p></footer>;
}

function App() {
  return (
    <div>
      <Header />
      <Content />
      <Footer />
    </div>
  );
}`}</pre>
      </div>

      <div className="info-box">
        <strong>Best Practices:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li>Lun s dng functional components vi hooks</li>
          <li>t tn component r rng, m t chc nng</li>
          <li>Gi component nh gn, tp trung vo mt nhim v</li>
          <li>Tch component ln thnh cc component nh hn</li>
          <li>Mt component nn c mt trch nhim duy nht (Single Responsibility Principle)</li>
        </ul>
      </div>
    </div>
  );
}

export default Components;

