const FILTERS = [
  { id: 'all', label: 'Tt c' },
  { id: 'active', label: 'ang lm' },
  { id: 'completed', label: 'Hon thnh' },
  { id: 'high', label: 'u tin cao' },
];

function TodoHeader({ stats, filter, onFilterChange, searchTerm, onSearchChange }) {
  return (
    <header className="todo-header">
      <div className="todo-header__top">
        <div>
          <div className="todo-header__title">Flowboard</div>
          <div className="todo-header__subtitle">Qun l tasks tp trung trong mt bng duy nht</div>
        </div>

        <div className="todo-header__stats">
          <div className="todo-stat">
            <span>Tng</span>
            <strong>{stats.total}</strong>
          </div>
          <div className="todo-stat">
            <span>Hon thnh</span>
            <strong>{stats.completed}</strong>
          </div>
          <div className="todo-stat">
            <span>ang ch</span>
            <strong>{stats.upcoming}</strong>
          </div>
        </div>
      </div>

      <div className="todo-controls">
        <div className="filter-group">
          {FILTERS.map(option => (
            <button
              key={option.id}
              className={`filter-button ${filter === option.id ? 'active' : ''}`}
              onClick={() => onFilterChange(option.id)}
            >
              {option.label}
            </button>
          ))}
        </div>

        <input
          className="search-input"
          placeholder="Tm kim cng vic..."
          value={searchTerm}
          onChange={e => onSearchChange(e.target.value)}
        />
      </div>
    </header>
  );
}

export default TodoHeader;

