import { useState } from 'react';

function AsyncAwait() {
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(false);

  const fetchWithAsync = async () => {
    setLoading(true);
    try {
      const response = await fetch('https://jsonplaceholder.typicode.com/users/1');
      if (!response.ok) throw new Error('Failed to fetch');
      const result = await response.json();
      setData(result);
    } catch (error) {
      console.error('Error:', error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="topic-section">
      <h2>Async/Await Patterns</h2>
      
      <div className="definition-box">
        <h4>nh ngha</h4>
        <p>
          <strong>async/await</strong> l c php JavaScript cho php vit code bt ng b 
          theo cch ng b, d c hn so vi Promise chains. Function c nh du 
          <span className="highlight"> async</span> s tr v mt Promise, v 
          <span className="highlight"> await</span> s ch Promise resolve trc khi tip tc.
        </p>
      </div>

      <h3>C php c bn</h3>
      <div className="code-example">
        <pre>{`// Function async
async function fetchData() {
  const response = await fetch('https://api.example.com/data');
  const data = await response.json();
  return data;
}

// S dng
fetchData()
  .then(data => console.log(data))
  .catch(error => console.error(error));`}</pre>
      </div>

      <h3>So snh Promise vs Async/Await</h3>
      <p>
        Async/await lm code d c v d maintain hn:
      </p>

      <div className="code-example">
        <pre>{`//  Promise chain (nested)
fetch('https://api.example.com/users')
  .then(response => response.json())
  .then(users => {
    return fetch(\`https://api.example.com/users/\${users[0].id}/posts\`);
  })
  .then(response => response.json())
  .then(posts => {
    console.log(posts);
  })
  .catch(error => {
    console.error(error);
  });

//  Async/await (linear)
async function getUserPosts() {
  try {
    const usersResponse = await fetch('https://api.example.com/users');
    const users = await usersResponse.json();
    
    const postsResponse = await fetch(\`https://api.example.com/users/\${users[0].id}/posts\`);
    const posts = await postsResponse.json();
    
    console.log(posts);
  } catch (error) {
    console.error(error);
  }
}`}</pre>
      </div>

      <h3>Error Handling vi try/catch</h3>
      <p>
        Async/await s dng try/catch  x l errors:
      </p>

      <div className="code-example">
        <pre>{`async function fetchUserData(userId) {
  try {
    const response = await fetch(\`https://api.example.com/users/\${userId}\`);
    
    if (!response.ok) {
      throw new Error(\`HTTP error! status: \${response.status}\`);
    }
    
    const user = await response.json();
    return user;
  } catch (error) {
    console.error('Error fetching user:', error);
    // C th throw li hoc return default value
    throw error;
  }
}`}</pre>
      </div>

      <h3>Multiple Async Operations</h3>
      <p>
        C th chy nhiu async operations song song hoc tun t:
      </p>

      <div className="code-example">
        <pre>{`// Chy tun t (chm hn)
async function fetchSequential() {
  const user = await fetch('/api/user');
  const posts = await fetch('/api/posts');
  const comments = await fetch('/api/comments');
  return { user, posts, comments };
}

// Chy song song (nhanh hn)
async function fetchParallel() {
  const [user, posts, comments] = await Promise.all([
    fetch('/api/user').then(r => r.json()),
    fetch('/api/posts').then(r => r.json()),
    fetch('/api/comments').then(r => r.json())
  ]);
  return { user, posts, comments };
}

// Hoc vi destructuring
async function fetchParallel2() {
  const userPromise = fetch('/api/user').then(r => r.json());
  const postsPromise = fetch('/api/posts').then(r => r.json());
  const commentsPromise = fetch('/api/comments').then(r => r.json());
  
  const user = await userPromise;
  const posts = await postsPromise;
  const comments = await commentsPromise;
  
  return { user, posts, comments };
}`}</pre>
      </div>

      <h3>Async trong React Components</h3>
      <p>
        S dng async/await trong event handlers v useEffect:
      </p>

      <div className="code-example">
        <pre>{`function UserProfile({ userId }) {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  
  useEffect(() => {
    async function loadUser() {
      try {
        setLoading(true);
        const response = await fetch(\`/api/users/\${userId}\`);
        const userData = await response.json();
        setUser(userData);
      } catch (error) {
        console.error('Failed to load user:', error);
      } finally {
        setLoading(false);
      }
    }
    
    loadUser();
  }, [userId]);
  
  const handleSave = async () => {
    try {
      await fetch(\`/api/users/\${userId}\`, {
        method: 'PUT',
        body: JSON.stringify(user)
      });
      alert('Saved!');
    } catch (error) {
      alert('Failed to save');
    }
  };
  
  if (loading) return <div>Loading...</div>;
  return <div>{/* User profile */}</div>;
}`}</pre>
      </div>

      <div className="interactive-demo">
        <h4>V d tng tc: Async/Await</h4>
        <button
          onClick={fetchWithAsync}
          disabled={loading}
          style={{
            padding: '10px 20px',
            background: '#667eea',
            color: 'white',
            border: 'none',
            borderRadius: '5px',
            cursor: 'pointer',
            marginBottom: '15px'
          }}
        >
          {loading ? 'ang ti...' : 'Fetch vi Async/Await'}
        </button>
        {data && (
          <div style={{ padding: '15px', background: '#f5f5f5', borderRadius: '5px' }}>
            <p><strong>Name:</strong> {data.name}</p>
            <p><strong>Email:</strong> {data.email}</p>
            <p><strong>Website:</strong> {data.website}</p>
          </div>
        )}
      </div>

      <div className="warning-box">
        <strong>Lu  quan trng:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li>Khng th s dng await  top-level code (tr modules ES2022+)</li>
          <li>Async function lun tr v Promise</li>
          <li>Khng th await trong non-async function</li>
          <li>Lun s dng try/catch  x l errors</li>
          <li>Trnh await trong loops, s dng Promise.all() thay th</li>
        </ul>
      </div>

      <div className="info-box">
        <strong>Best Practices:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li>u tin async/await cho code d c</li>
          <li>S dng Promise.all() cho parallel operations</li>
          <li>Lun x l errors vi try/catch</li>
          <li>S dng finally  cleanup</li>
          <li>Trnh nested async/await qu su</li>
        </ul>
      </div>
    </div>
  );
}

export default AsyncAwait;

