import { useState } from 'react';

function MappingArrayData() {
  const [users] = useState([
    { id: 1, name: 'John Doe', email: 'john@example.com', role: 'admin' },
    { id: 2, name: 'Jane Smith', email: 'jane@example.com', role: 'user' },
    { id: 3, name: 'Bob Johnson', email: 'bob@example.com', role: 'user' },
  ]);

  return (
    <div className="topic-section">
      <h2>Mapping Array Data to Components</h2>
      
      <div className="definition-box">
        <h4>nh ngha</h4>
        <p>
          <strong>Mapping array data</strong> l k thut chuyn i mng d liu thnh 
          danh sch cc React components. y l pattern ph bin khi hin th danh sch 
          items t API hoc state. S dng phng thc <span className="highlight">map()</span> 
           to ra mt mng cc JSX elements.
        </p>
      </div>

      <h3>C php c bn</h3>
      <div className="code-example">
        <pre>{`const items = ['Apple', 'Banana', 'Orange'];

function FruitList() {
  return (
    <ul>
      {items.map((item, index) => (
        <li key={index}>{item}</li>
      ))}
    </ul>
  );
}`}</pre>
      </div>

      <h3>Mapping vi Objects</h3>
      <p>
        Khi map array of objects, truy cp properties v s dng unique keys:
      </p>

      <div className="code-example">
        <pre>{`const users = [
  { id: 1, name: 'John', email: 'john@example.com' },
  { id: 2, name: 'Jane', email: 'jane@example.com' },
  { id: 3, name: 'Bob', email: 'bob@example.com' }
];

function UserList() {
  return (
    <div>
      {users.map(user => (
        <div key={user.id}>
          <h3>{user.name}</h3>
          <p>{user.email}</p>
        </div>
      ))}
    </div>
  );
}`}</pre>
      </div>

      <h3>Mapping vi API Data</h3>
      <p>
        Kt hp vi async data t API:
      </p>

      <div className="code-example">
        <pre>{`function UserList() {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  
  useEffect(() => {
    async function fetchUsers() {
      const response = await fetch('/api/users');
      const data = await response.json();
      setUsers(data);
      setLoading(false);
    }
    fetchUsers();
  }, []);
  
  if (loading) return <div>Loading...</div>;
  
  return (
    <div>
      {users.map(user => (
        <UserCard key={user.id} user={user} />
      ))}
    </div>
  );
}

function UserCard({ user }) {
  return (
    <div>
      <h3>{user.name}</h3>
      <p>{user.email}</p>
    </div>
  );
}`}</pre>
      </div>

      <h3>Conditional Rendering trong Map</h3>
      <p>
        C th kt hp vi conditional rendering:
      </p>

      <div className="code-example">
        <pre>{`function TodoList({ todos }) {
  return (
    <div>
      {todos.length === 0 ? (
        <p>No todos yet</p>
      ) : (
        todos.map(todo => (
          <TodoItem key={todo.id} todo={todo} />
        ))
      )}
    </div>
  );
}

// Hoc filter trc khi map
function ActiveTodos({ todos }) {
  const activeTodos = todos.filter(todo => !todo.completed);
  
  return (
    <div>
      {activeTodos.map(todo => (
        <TodoItem key={todo.id} todo={todo} />
      ))}
    </div>
  );
}`}</pre>
      </div>

      <h3>Nested Mapping</h3>
      <p>
        C th map nested arrays:
      </p>

      <div className="code-example">
        <pre>{`const categories = [
  {
    id: 1,
    name: 'Frontend',
    items: ['React', 'Vue', 'Angular']
  },
  {
    id: 2,
    name: 'Backend',
    items: ['Node.js', 'Python', 'Java']
  }
];

function CategoryList() {
  return (
    <div>
      {categories.map(category => (
        <div key={category.id}>
          <h3>{category.name}</h3>
          <ul>
            {category.items.map((item, index) => (
              <li key={index}>{item}</li>
            ))}
          </ul>
        </div>
      ))}
    </div>
  );
}`}</pre>
      </div>

      <div className="interactive-demo">
        <h4>V d tng tc: Mapping Users</h4>
        <div style={{ display: 'grid', gap: '12px', marginTop: '15px' }}>
          {users.map(user => (
            <div
              key={user.id}
              style={{
                padding: '15px',
                background: '#f5f5f5',
                borderRadius: '8px',
                border: '1px solid #ddd'
              }}
            >
              <h4 style={{ margin: '0 0 8px 0', color: '#667eea' }}>{user.name}</h4>
              <p style={{ margin: '0 0 4px 0', fontSize: '0.9rem' }}>Email: {user.email}</p>
              <span
                style={{
                  display: 'inline-block',
                  padding: '4px 8px',
                  background: user.role === 'admin' ? '#4caf50' : '#2196f3',
                  color: 'white',
                  borderRadius: '4px',
                  fontSize: '0.8rem'
                }}
              >
                {user.role}
              </span>
            </div>
          ))}
        </div>
      </div>

      <h3>Performance Considerations</h3>
      <div className="warning-box">
        <strong>Lu  v Performance:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li>Lun s dng unique, stable keys (khng dng index nu c th)</li>
          <li>Vi danh sch ln, cn nhc virtualization (react-window)</li>
          <li>Trnh to functions trong map (s dng useCallback nu cn)</li>
          <li>Memoize expensive computations vi useMemo</li>
        </ul>
      </div>

      <div className="code-example">
        <pre>{`//  To function mi mi ln render
function UserList({ users }) {
  return (
    <div>
      {users.map(user => (
        <UserCard
          key={user.id}
          user={user}
          onClick={() => handleClick(user.id)} //  Function mi mi ln
        />
      ))}
    </div>
  );
}

//  S dng useCallback
function UserList({ users }) {
  const handleClick = useCallback((userId) => {
    // Handle click
  }, []);
  
  return (
    <div>
      {users.map(user => (
        <UserCard
          key={user.id}
          user={user}
          onClick={handleClick} //  Same function reference
        />
      ))}
    </div>
  );
}`}</pre>
      </div>

      <div className="info-box">
        <strong>Best Practices:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li>Lun s dng key prop vi unique values</li>
          <li>Trnh dng index lm key nu list c th thay i</li>
          <li>Extract mapping logic thnh separate components</li>
          <li>X l empty states</li>
          <li>Ti u performance cho large lists</li>
        </ul>
      </div>
    </div>
  );
}

export default MappingArrayData;

