import { useState, useEffect } from 'react';

function Timer() {
  const [seconds, setSeconds] = useState(0);
  const [isRunning, setIsRunning] = useState(false);

  useEffect(() => {
    let interval = null;
    if (isRunning) {
      interval = setInterval(() => {
        setSeconds(prev => prev + 1);
      }, 1000);
    }
    return () => {
      if (interval) clearInterval(interval);
    };
  }, [isRunning]);

  return (
    <div style={{ textAlign: 'center', padding: '20px' }}>
      <h2>{seconds}s</h2>
      <button onClick={() => setIsRunning(!isRunning)}>
        {isRunning ? 'Dng' : 'Bt u'}
      </button>
      <button onClick={() => setSeconds(0)} style={{ marginLeft: '10px' }}>
        Reset
      </button>
    </div>
  );
}

function DataFetcher() {
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    // Simulate API call
    const fetchData = async () => {
      try {
        setLoading(true);
        await new Promise(resolve => setTimeout(resolve, 1000));
        setData({ message: 'D liu  ti thnh cng!' });
        setError(null);
      } catch (err) {
        setError('C li xy ra');
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  if (loading) return <p>ang ti...</p>;
  if (error) return <p style={{ color: 'red' }}>{error}</p>;
  return <p style={{ color: 'green' }}>{data?.message}</p>;
}

function Effects() {
  const [count, setCount] = useState(0);
  const [name, setName] = useState('');

  // Effect chy mi ln render
  useEffect(() => {
    console.log('Component  render');
  });

  // Effect chy mt ln khi mount
  useEffect(() => {
    console.log('Component  mount');
  }, []);

  // Effect chy khi count thay i
  useEffect(() => {
    console.log('Count  thay i:', count);
  }, [count]);

  return (
    <div className="topic-section">
      <h2>Side Effects vi useEffect Hook</h2>
      
      <div className="definition-box">
        <h4>nh ngha</h4>
        <p>
          <strong>Side Effects</strong> l cc thao tc khng lin quan trc tip n 
          vic render UI, nh: fetch data, set up subscriptions, thay i DOM th cng, 
          timers, v.v. <span className="highlight">useEffect</span> l React Hook cho php 
          bn thc hin side effects trong functional components.
        </p>
      </div>

      <h3>C php useEffect</h3>
      <div className="code-example">
        <pre>{`import { useEffect } from 'react';

function Component() {
  useEffect(() => {
    // Side effect code
    // Chy sau mi ln render
    
    return () => {
      // Cleanup function (optional)
      // Chy trc khi component unmount hoc trc effect chy li
    };
  }, [dependencies]); // Dependency array
  
  return <div>Content</div>;
}`}</pre>
      </div>

      <h3>useEffect khng c dependencies</h3>
      <p>
        Nu khng c dependency array, effect s chy sau mi ln component render.
      </p>

      <div className="code-example">
        <pre>{`function Component() {
  useEffect(() => {
    console.log('Chy sau mi ln render');
  }); // Khng c dependency array
  
  return <div>Content</div>;
}`}</pre>
      </div>

      <div className="warning-box">
        <strong>Lu :</strong> Effect ny chy sau mi ln render, c th gy ra 
        vn  hiu sut nu khng cn thn. Thng ch dng cho logging hoc 
        cc thao tc khng tn km.
      </div>

      <h3>useEffect vi empty dependency array</h3>
      <p>
        Vi dependency array rng <span className="highlight">[]</span>, effect ch chy 
        mt ln sau khi component mount (tng ng componentDidMount trong class components).
      </p>

      <div className="code-example">
        <pre>{`function Component() {
  useEffect(() => {
    console.log('Ch chy mt ln khi component mount');
    // Thng dng :
    // - Fetch data t API
    // - Set up subscriptions
    // - Thm event listeners
  }, []); // Empty dependency array
  
  return <div>Content</div>;
}`}</pre>
      </div>

      <div className="interactive-demo">
        <h4>V d: Fetch Data khi mount</h4>
        <DataFetcher />
      </div>

      <h3>useEffect vi dependencies</h3>
      <p>
        Khi c dependencies trong array, effect s chy li mi khi mt trong cc 
        dependencies thay i.
      </p>

      <div className="code-example">
        <pre>{`function Component() {
  const [count, setCount] = useState(0);
  const [name, setName] = useState('');
  
  // Chy khi count thay i
  useEffect(() => {
    console.log('Count  thay i:', count);
  }, [count]);
  
  // Chy khi count hoc name thay i
  useEffect(() => {
    console.log('Count hoc name  thay i');
  }, [count, name]);
  
  return <div>Content</div>;
}`}</pre>
      </div>

      <div className="interactive-demo">
        <h4>V d: Timer vi useEffect</h4>
        <Timer />
      </div>

      <h3>Cleanup Function</h3>
      <p>
        Cleanup function c return t effect s chy:
      </p>
      <ul style={{ paddingLeft: '20px', lineHeight: '1.8' }}>
        <li>Trc khi component unmount</li>
        <li>Trc khi effect chy li (nu c dependencies)</li>
      </ul>

      <div className="code-example">
        <pre>{`function Component() {
  useEffect(() => {
    const interval = setInterval(() => {
      console.log('Tick');
    }, 1000);
    
    // Cleanup function
    return () => {
      clearInterval(interval); // Dn dp khi component unmount
    };
  }, []);
  
  return <div>Content</div>;
}`}</pre>
      </div>

      <h3>Cc trng hp s dng ph bin</h3>

      <h4>1. Fetch Data t API</h4>
      <div className="code-example">
        <pre>{`function UserProfile({ userId }) {
  const [user, setUser] = useState(null);
  
  useEffect(() => {
    async function fetchUser() {
      const response = await fetch(\`/api/users/\${userId}\`);
      const data = await response.json();
      setUser(data);
    }
    
    fetchUser();
  }, [userId]); // Chy li khi userId thay i
  
  if (!user) return <div>Loading...</div>;
  return <div>{user.name}</div>;
}`}</pre>
      </div>

      <h4>2. Set up Event Listeners</h4>
      <div className="code-example">
        <pre>{`function WindowSize() {
  const [size, setSize] = useState({
    width: window.innerWidth,
    height: window.innerHeight
  });
  
  useEffect(() => {
    function handleResize() {
      setSize({
        width: window.innerWidth,
        height: window.innerHeight
      });
    }
    
    window.addEventListener('resize', handleResize);
    
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);
  
  return <div>Width: {size.width}, Height: {size.height}</div>;
}`}</pre>
      </div>

      <h4>3. Subscriptions</h4>
      <div className="code-example">
        <pre>{`function ChatRoom({ roomId }) {
  useEffect(() => {
    const subscription = subscribeToRoom(roomId, (message) => {
      console.log('New message:', message);
    });
    
    return () => {
      subscription.unsubscribe();
    };
  }, [roomId]);
  
  return <div>Chat Room {roomId}</div>;
}`}</pre>
      </div>

      <h3>Li thng gp</h3>

      <div className="warning-box">
        <strong>1. Missing Dependencies:</strong>
        <p>Nu bn s dng state hoc props trong effect nhng khng thm vo dependency array, 
        bn c th gp li stale closure.</p>
      </div>

      <div className="code-example">
        <pre>{`//  SAI - Thiu dependency
function Component() {
  const [count, setCount] = useState(0);
  
  useEffect(() => {
    console.log(count); // C th khng phn nh gi tr mi nht
  }, []); // Thiu [count]
}

//  NG
function Component() {
  const [count, setCount] = useState(0);
  
  useEffect(() => {
    console.log(count);
  }, [count]); // C [count]
}`}</pre>
      </div>

      <div className="warning-box">
        <strong>2. Infinite Loops:</strong>
        <p>Nu effect cp nht state v state  nm trong dependency array, 
        c th gy ra vng lp v hn.</p>
      </div>

      <div className="code-example">
        <pre>{`//  SAI - Infinite loop
function Component() {
  const [count, setCount] = useState(0);
  
  useEffect(() => {
    setCount(count + 1); // Cp nht count
  }, [count]); // count trong dependencies  vng lp v hn
}

//  NG - S dng functional update hoc iu kin
function Component() {
  const [count, setCount] = useState(0);
  
  useEffect(() => {
    if (count < 10) {
      setCount(prev => prev + 1);
    }
  }, [count]);
}`}</pre>
      </div>

      <h3>useEffect vs Lifecycle Methods</h3>
      <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '20px', margin: '20px 0' }}>
        <div style={{ background: '#e3f2fd', padding: '15px', borderRadius: '8px' }}>
          <h4 style={{ color: '#1976d2', marginBottom: '10px' }}>Class Components</h4>
          <ul style={{ paddingLeft: '20px', lineHeight: '1.8' }}>
            <li>componentDidMount</li>
            <li>componentDidUpdate</li>
            <li>componentWillUnmount</li>
          </ul>
        </div>

        <div style={{ background: '#f3e5f5', padding: '15px', borderRadius: '8px' }}>
          <h4 style={{ color: '#7b1fa2', marginBottom: '10px' }}>Functional Components</h4>
          <ul style={{ paddingLeft: '20px', lineHeight: '1.8' }}>
            <li>useEffect vi empty deps</li>
            <li>useEffect vi dependencies</li>
            <li>useEffect vi cleanup function</li>
          </ul>
        </div>
      </div>

      <div className="info-box">
        <strong>Best Practices:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li>Lun thm dependencies vo dependency array</li>
          <li>Cleanup subscriptions, timers, event listeners</li>
          <li>Tch effects khc nhau thnh cc useEffect ring bit</li>
          <li>S dng ESLint plugin  kim tra dependencies</li>
          <li>Trnh side effects trong render phase</li>
        </ul>
      </div>
    </div>
  );
}

export default Effects;

