import { useEffect, useState } from "react";

const ENV_API_KEY =
    process.env.REACT_APP_OPENWEATHER_API_KEY;
const WEATHER_ENDPOINT =
    "https://api.openweathermap.org/data/3.0/onecall";
const REVERSE_GEOCODE_ENDPOINT =
    "https://api.openweathermap.org/geo/1.0/reverse";
const STORAGE_KEY = "weather_info";
const CACHE_DURATION = 1800; // 30 pht

const formatNumber = (value) =>
    typeof value === "number" ? value.toFixed(2) : "--";

const loadCachedWeather = () => {
    if (typeof window === "undefined") return null;
    try {
        const raw =
            window.localStorage.getItem(STORAGE_KEY);
        if (raw) {
            const data = JSON.parse(raw);
            const now = Math.floor(Date.now() / 1000);
            if (
                data.next_refresh &&
                now < data.next_refresh
            ) {
                return data;
            }
        }
    } catch (error) {
        console.warn(
            "Khng th c d liu weather_info t localStorage:",
            error
        );
    }
    return null;
};

const loadRawWeatherCache = () => {
    if (typeof window === "undefined") return null;
    try {
        const raw =
            window.localStorage.getItem(STORAGE_KEY);
        if (raw) {
            return JSON.parse(raw);
        }
    } catch (error) {
        console.warn(
            "Khng th c raw weather_info t localStorage:",
            error
        );
    }
    return null;
};

const saveWeatherToCache = (weatherData) => {
    if (typeof window === "undefined") return;
    try {
        const now = Math.floor(Date.now() / 1000);
        const existing = loadRawWeatherCache() || {};
        const data = {
            ...existing,
            ...weatherData,
            next_refresh: now + CACHE_DURATION,
        };
        window.localStorage.setItem(
            STORAGE_KEY,
            JSON.stringify(data)
        );
    } catch (error) {
        console.warn(
            "Khng th lu d liu weather_info vo localStorage:",
            error
        );
    }
};

function WeatherPanel() {
    const [coords, setCoords] = useState(null);
    const [weather, setWeather] = useState(null);
    const [location, setLocation] = useState(null);
    const [status, setStatus] = useState("idle"); // idle | loading | success | error
    const [error, setError] = useState("");
    const [apiKeyInput, setApiKeyInput] = useState("");
    const [showApiKeyInput, setShowApiKeyInput] =
        useState(false);

    const fetchWeather = async (lat, lon, keyToUse) => {
        try {
            const weatherResponse = await fetch(
                `${WEATHER_ENDPOINT}?lat=${lat}&lon=${lon}&units=metric&exclude=minutely,hourly,daily,alerts&appid=${keyToUse}`
            );

            if (!weatherResponse.ok) {
                throw new Error(
                    "Khng th ti d liu thi tit."
                );
            }

            const weatherData =
                await weatherResponse.json();

            const locationResponse = await fetch(
                `${REVERSE_GEOCODE_ENDPOINT}?lat=${lat}&lon=${lon}&limit=1&appid=${keyToUse}`
            );

            let locationData = null;
            if (locationResponse.ok) {
                const [place] =
                    await locationResponse.json();
                if (place) {
                    locationData = {
                        city: place.name,
                        country: place.country,
                    };
                    setLocation(locationData);
                }
            }

            const weatherInfo = {
                temp: weatherData?.current?.temp ?? 0,
                description:
                    weatherData?.current?.weather?.[0]
                        ?.description ?? "Khng xc nh",
                timezone: weatherData?.timezone,
            };
            setWeather(weatherInfo);

            // Lu vo localStorage
            saveWeatherToCache({
                coords: { lat, lon },
                weather: weatherInfo,
                location: locationData,
            });

            setStatus("success");
            setError("");
        } catch (err) {
            setStatus("error");
            setError(
                err.message ||
                    " xy ra li khi ti d liu thi tit."
            );
            setShowApiKeyInput(true);
        }
    };

    useEffect(() => {
        // Ly raw cache  c api_key (khng ph thuc thi gian ht hn)
        const rawCache = loadRawWeatherCache();
        const cachedApiKey = rawCache?.api_key || "";

        const effectiveApiKey =
            ENV_API_KEY || cachedApiKey || "";
        if (effectiveApiKey) {
            setApiKeyInput(effectiveApiKey);
        }

        // Kim tra cache thi tit trc
        const cached = loadCachedWeather();
        if (cached) {
            setCoords(cached.coords);
            setWeather(cached.weather);
            setLocation(cached.location);
            setStatus("success");
            return;
        }

        // Nu khng c API key no c th yu cu nhp
        if (!effectiveApiKey) {
            setStatus("error");
            setError(
                "Cha c API key ca OpenWeather. Vui lng nhp bn di."
            );
            setShowApiKeyInput(true);
            return;
        }

        // Nu khng c cache hoc  ht hn, ly geolocation v fetch mi
        if (!navigator.geolocation) {
            setStatus("error");
            setError(
                "Trnh duyt khng h tr xc nh v tr."
            );
            return;
        }

        setStatus("loading");
        navigator.geolocation.getCurrentPosition(
            (position) => {
                const { latitude, longitude } =
                    position.coords;
                setCoords({
                    lat: latitude,
                    lon: longitude,
                });
                fetchWeather(
                    latitude,
                    longitude,
                    effectiveApiKey
                );
            },
            (geoError) => {
                setStatus("error");
                setError(
                    geoError.message ||
                        "Khng th truy cp v tr. Hy cp quyn cho trnh duyt."
                );
            },
            { enableHighAccuracy: true, timeout: 10000 }
        );
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const handleSaveApiKey = () => {
        const newKey = apiKeyInput.trim();
        if (!newKey) return;

        try {
            const existing = loadRawWeatherCache() || {};
            const updated = {
                ...existing,
                api_key: newKey,
            };
            if (typeof window !== "undefined") {
                window.localStorage.setItem(
                    STORAGE_KEY,
                    JSON.stringify(updated)
                );
            }
            setShowApiKeyInput(false);
            setStatus("loading");
            setError("");

            // Th reload weather vi API key mi
            if (coords) {
                fetchWeather(
                    coords.lat,
                    coords.lon,
                    newKey
                );
            } else if (navigator.geolocation) {
                navigator.geolocation.getCurrentPosition(
                    (position) => {
                        const { latitude, longitude } =
                            position.coords;
                        setCoords({
                            lat: latitude,
                            lon: longitude,
                        });
                        fetchWeather(
                            latitude,
                            longitude,
                            newKey
                        );
                    },
                    (geoError) => {
                        setStatus("error");
                        setError(
                            geoError.message ||
                                "Khng th truy cp v tr. Hy cp quyn cho trnh duyt."
                        );
                    },
                    {
                        enableHighAccuracy: true,
                        timeout: 10000,
                    }
                );
            } else {
                setStatus("error");
                setError(
                    "Trnh duyt khng h tr xc nh v tr."
                );
            }
        } catch (e) {
            console.warn(
                "Khng th lu API key vo localStorage:",
                e
            );
            setStatus("error");
            setError(
                "Khng th lu API key vo localStorage."
            );
            setShowApiKeyInput(true);
        }
    };

    const renderContent = () => {
        if (status === "loading") {
            return (
                <div className="weather-loading">
                    ang ly d liu thi tit...
                </div>
            );
        }

        if (status === "error") {
            return (
                <div className="weather-error">
                    <div>{error}</div>
                    {showApiKeyInput && (
                        <div style={{ marginTop: 12 }}>
                            <div
                                style={{
                                    marginBottom: 4,
                                    fontWeight: 500,
                                }}
                            >
                                Nhp API key OpenWeather (s
                                c lu trong trnh duyt
                                ca bn):
                            </div>
                            <div
                                style={{
                                    display: "flex",
                                    gap: 8,
                                }}
                            >
                                <input
                                    type="text"
                                    value={apiKeyInput}
                                    onChange={(e) =>
                                        setApiKeyInput(
                                            e.target.value
                                        )
                                    }
                                    placeholder="Nhp API key..."
                                    style={{
                                        flex: 1,
                                        padding: "6px 8px",
                                        borderRadius: 4,
                                        border: "1px solid #cbd5f5",
                                    }}
                                />
                                <button
                                    type="button"
                                    onClick={
                                        handleSaveApiKey
                                    }
                                    style={{
                                        padding: "6px 12px",
                                        borderRadius: 4,
                                        border: "none",
                                        backgroundColor:
                                            "#2563eb",
                                        color: "#fff",
                                        cursor: "pointer",
                                        fontWeight: 500,
                                    }}
                                >
                                    Lu
                                </button>
                            </div>
                        </div>
                    )}
                </div>
            );
        }

        if (weather) {
            return (
                <>
                    <div className="weather-card__main">
                        <div className="weather-temp">
                            {Math.round(weather.temp)}C
                        </div>
                        <div style={{ textAlign: "right" }}>
                            <div
                                style={{
                                    fontSize: "1.1rem",
                                    fontWeight: 600,
                                    textTransform:
                                        "capitalize",
                                }}
                            >
                                {weather.description}
                            </div>
                            <div
                                style={{
                                    color: "#94a3b8",
                                    marginTop: 4,
                                }}
                            >
                                {location?.city ||
                                    "Khng r"}
                                ,{" "}
                                {location?.country || "--"}
                            </div>
                        </div>
                    </div>

                    <div className="weather-meta">
                        <span>
                            <strong>Lat/Lon:</strong>{" "}
                            {formatNumber(coords?.lat)},{" "}
                            {formatNumber(coords?.lon)}
                        </span>
                        <span>
                            <strong>Mi gi:</strong>{" "}
                            {weather.timezone || "--"}
                        </span>
                        <span>
                            <strong>V tr:</strong>{" "}
                            {location
                                ? `${location.city}, ${location.country}`
                                : "ang cp nht"}
                        </span>
                    </div>
                </>
            );
        }

        return null;
    };

    return (
        <div className="weather-card">
            {renderContent()}
        </div>
    );
}

export default WeatherPanel;
