import { useState } from 'react';

function FetchAPI() {
  const [exampleData, setExampleData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  const handleFetch = async () => {
    setLoading(true);
    setError('');
    try {
      const response = await fetch('https://jsonplaceholder.typicode.com/posts/1');
      if (!response.ok) {
        throw new Error('Network response was not ok');
      }
      const data = await response.json();
      setExampleData(data);
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="topic-section">
      <h2>Fetch API and Promises</h2>
      
      <div className="definition-box">
        <h4>nh ngha</h4>
        <p>
          <strong>Fetch API</strong> l mt interface hin i trong JavaScript  thc hin 
          HTTP requests. N tr v mt <span className="highlight">Promise</span> - mt object 
          i din cho kt qu ca mt thao tc bt ng b c th thnh cng hoc tht bi.
        </p>
      </div>

      <h3>C php c bn</h3>
      <div className="code-example">
        <pre>{`// Fetch c bn
fetch('https://api.example.com/data')
  .then(response => response.json())
  .then(data => console.log(data))
  .catch(error => console.error('Error:', error));`}</pre>
      </div>

      <h3>Promise Chain</h3>
      <p>
        Fetch tr v mt Promise, bn c th chain cc .then()  x l response:
      </p>

      <div className="code-example">
        <pre>{`fetch('https://api.example.com/users')
  .then(response => {
    // Kim tra status code
    if (!response.ok) {
      throw new Error('Network response was not ok');
    }
    return response.json(); // Parse JSON
  })
  .then(data => {
    // X l d liu
    console.log('Users:', data);
    return data;
  })
  .then(users => {
    // C th chain thm
    return users.filter(user => user.active);
  })
  .catch(error => {
    // X l li
    console.error('Fetch error:', error);
  });`}</pre>
      </div>

      <h3>Fetch vi Options</h3>
      <p>
        Fetch c th nhn tham s th hai l options object  cu hnh request:
      </p>

      <div className="code-example">
        <pre>{`fetch('https://api.example.com/users', {
  method: 'POST',
  headers: {
    'Content-Type': 'application/json',
    'Authorization': 'Bearer token123'
  },
  body: JSON.stringify({
    name: 'John',
    email: 'john@example.com'
  })
})
  .then(response => response.json())
  .then(data => console.log(data));`}</pre>
      </div>

      <h3>Response Methods</h3>
      <p>
        Response object c nhiu methods  c d liu:
      </p>

      <div className="code-example">
        <pre>{`fetch('https://api.example.com/data')
  .then(response => {
    // response.json() - Parse JSON
    // response.text() - Parse text
    // response.blob() - Parse binary data
    // response.arrayBuffer() - Parse ArrayBuffer
    
    const contentType = response.headers.get('content-type');
    if (contentType.includes('application/json')) {
      return response.json();
    } else {
      return response.text();
    }
  })
  .then(data => console.log(data));`}</pre>
      </div>

      <h3>Error Handling</h3>
      <p>
        Quan trng: Fetch ch reject Promise khi c network error, khng reject vi HTTP error status:
      </p>

      <div className="code-example">
        <pre>{`fetch('https://api.example.com/data')
  .then(response => {
    // Fetch khng t ng reject vi 404, 500, etc.
    if (!response.ok) {
      throw new Error(\`HTTP error! status: \${response.status}\`);
    }
    return response.json();
  })
  .then(data => console.log(data))
  .catch(error => {
    // Bt c network errors v HTTP errors
    console.error('Error:', error);
  });`}</pre>
      </div>

      <div className="interactive-demo">
        <h4>V d tng tc: Fetch API</h4>
        <button
          onClick={handleFetch}
          disabled={loading}
          style={{
            padding: '10px 20px',
            background: '#667eea',
            color: 'white',
            border: 'none',
            borderRadius: '5px',
            cursor: 'pointer',
            marginBottom: '15px'
          }}
        >
          {loading ? 'ang ti...' : 'Fetch Data'}
        </button>
        {error && (
          <div style={{ color: '#f44336', marginBottom: '10px' }}>Error: {error}</div>
        )}
        {exampleData && (
          <div style={{ padding: '15px', background: '#f5f5f5', borderRadius: '5px' }}>
            <pre style={{ margin: 0, fontSize: '0.9rem' }}>
              {JSON.stringify(exampleData, null, 2)}
            </pre>
          </div>
        )}
      </div>

      <h3>Promise States</h3>
      <p>
        Promise c 3 trng thi:
      </p>

      <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr 1fr', gap: '15px', margin: '20px 0' }}>
        <div style={{ background: '#fff3e0', padding: '15px', borderRadius: '8px' }}>
          <h4 style={{ color: '#e65100', marginBottom: '8px' }}>Pending</h4>
          <p style={{ margin: 0, fontSize: '0.9rem' }}>ang ch kt qu</p>
        </div>
        <div style={{ background: '#e8f5e9', padding: '15px', borderRadius: '8px' }}>
          <h4 style={{ color: '#2e7d32', marginBottom: '8px' }}>Fulfilled</h4>
          <p style={{ margin: 0, fontSize: '0.9rem' }}>Thnh cng, c d liu</p>
        </div>
        <div style={{ background: '#ffebee', padding: '15px', borderRadius: '8px' }}>
          <h4 style={{ color: '#c62828', marginBottom: '8px' }}>Rejected</h4>
          <p style={{ margin: 0, fontSize: '0.9rem' }}>Tht bi, c li</p>
        </div>
      </div>

      <div className="info-box">
        <strong>Best Practices:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li>Lun kim tra response.ok trc khi parse</li>
          <li>S dng .catch()  x l errors</li>
          <li>X l timeout cho requests di</li>
          <li>S dng AbortController  cancel requests</li>
          <li>Validate response data trc khi s dng</li>
        </ul>
      </div>
    </div>
  );
}

export default FetchAPI;

