function EnvironmentVariables() {
  return (
    <div className="topic-section">
      <h2>Environment Variables for API Keys</h2>
      
      <div className="definition-box">
        <h4>nh ngha</h4>
        <p>
          <strong>Environment Variables</strong> l cc bin c nh ngha bn ngoi code, 
          thng dng  lu tr cu hnh, API keys, v cc thng tin nhy cm. Trong React, 
          environment variables c truy cp qua <span className="highlight">process.env</span> 
          v phi bt u vi <span className="highlight">REACT_APP_</span>  c expose ra client.
        </p>
      </div>

      <h3>To Environment Variables</h3>
      <p>
        To file <span className="highlight">.env</span> trong root directory ca project:
      </p>

      <div className="code-example">
        <pre>{`# .env
REACT_APP_API_URL=https://api.example.com
REACT_APP_API_KEY=your_api_key_here
REACT_APP_ENVIRONMENT=development

# Khng commit file .env vo git
# Thm vo .gitignore`}</pre>
      </div>

      <h3>S dng trong Code</h3>
      <div className="code-example">
        <pre>{`// S dng environment variables
const API_URL = process.env.REACT_APP_API_URL;
const API_KEY = process.env.REACT_APP_API_KEY;

async function fetchData() {
  const response = await fetch(\`\${API_URL}/users\`, {
    headers: {
      'Authorization': \`Bearer \${API_KEY}\`
    }
  });
  return await response.json();
}

// Hoc s dng trc tip
fetch(\`\${process.env.REACT_APP_API_URL}/users\`)
  .then(response => response.json())
  .then(data => console.log(data));`}</pre>
      </div>

      <h3>Environment Files</h3>
      <p>
        React h tr nhiu environment files:
      </p>

      <div className="code-example">
        <pre>{`# .env - Default cho tt c environments
REACT_APP_API_URL=https://api.example.com

# .env.local - Local overrides (khng commit)
REACT_APP_API_KEY=local_key_123

# .env.development - Ch cho development
REACT_APP_API_URL=http://localhost:3000/api

# .env.production - Ch cho production
REACT_APP_API_URL=https://api.production.com

# .env.test - Ch cho testing
REACT_APP_API_URL=http://localhost:3001/api`}</pre>
      </div>

      <h3>Default Values</h3>
      <p>
        Cung cp default values khi variable khng c set:
      </p>

      <div className="code-example">
        <pre>{`// S dng default value
const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:3000/api';
const API_KEY = process.env.REACT_APP_API_KEY || '';

// Hoc vi destructuring
const {
  REACT_APP_API_URL = 'http://localhost:3000/api',
  REACT_APP_API_KEY = ''
} = process.env;

// Validation
if (!process.env.REACT_APP_API_KEY) {
  console.warn('API_KEY is not set!');
}`}</pre>
      </div>

      <h3>Best Practices cho API Keys</h3>
      <div className="warning-box">
        <strong>Quan trng v Security:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li>KHNG commit .env files vo git</li>
          <li>Ch expose public API keys (client-side keys)</li>
          <li>Secret keys phi  server-side, khng bao gi expose</li>
          <li>S dng .env.example  document required variables</li>
          <li>Rotate API keys thng xuyn</li>
        </ul>
      </div>

      <div className="code-example">
        <pre>{`# .env.example (commit vo git)
REACT_APP_API_URL=https://api.example.com
REACT_APP_API_KEY=your_api_key_here
REACT_APP_ENVIRONMENT=development

# .env (KHNG commit)
REACT_APP_API_URL=https://api.example.com
REACT_APP_API_KEY=actual_secret_key_here
REACT_APP_ENVIRONMENT=development`}</pre>
      </div>

      <h3>Using trong React Components</h3>
      <div className="code-example">
        <pre>{`function WeatherComponent() {
  const API_KEY = process.env.REACT_APP_WEATHER_API_KEY;
  const API_URL = process.env.REACT_APP_WEATHER_API_URL;
  
  useEffect(() => {
    async function fetchWeather() {
      const response = await fetch(
        \`\${API_URL}?key=\${API_KEY}&q=London\`
      );
      const data = await response.json();
      console.log(data);
    }
    fetchWeather();
  }, []);
  
  return <div>Weather Component</div>;
}

// Hoc to config object
const config = {
  apiUrl: process.env.REACT_APP_API_URL || 'http://localhost:3000',
  apiKey: process.env.REACT_APP_API_KEY || '',
  environment: process.env.REACT_APP_ENVIRONMENT || 'development'
};

function App() {
  return (
    <div>
      <p>Environment: {config.environment}</p>
      <WeatherComponent />
    </div>
  );
}`}</pre>
      </div>

      <h3>TypeScript vi Environment Variables</h3>
      <div className="code-example">
        <pre>{`// src/types/env.d.ts
declare namespace NodeJS {
  interface ProcessEnv {
    REACT_APP_API_URL: string;
    REACT_APP_API_KEY: string;
    REACT_APP_ENVIRONMENT: 'development' | 'production' | 'test';
  }
}

// S dng vi type safety
const API_URL: string = process.env.REACT_APP_API_URL;
const ENV: 'development' | 'production' | 'test' = process.env.REACT_APP_ENVIRONMENT;`}</pre>
      </div>

      <h3>Build-time vs Runtime</h3>
      <div className="info-box">
        <strong>Lu :</strong>
        <p style={{ marginTop: '10px' }}>
          Environment variables trong React c embed vo bundle ti build time, khng phi runtime. 
          iu ny c ngha l bn phi rebuild app  thay i environment variables c hiu lc.
        </p>
      </div>

      <div className="code-example">
        <pre>{`// Build command
npm run build

// Environment variables c embed vo bundle
// Khng th thay i sau khi build

// Development
npm start
// S dng .env.development

// Production
npm run build
// S dng .env.production`}</pre>
      </div>

      <h3>Gitignore Setup</h3>
      <div className="code-example">
        <pre>{`# .gitignore
# Environment variables
.env
.env.local
.env.development.local
.env.test.local
.env.production.local

# Nhng commit .env.example
# .env.example`}</pre>
      </div>

      <div className="info-box">
        <strong>Best Practices:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li>Lun prefix vi REACT_APP_ cho client-side variables</li>
          <li>To .env.example  document required variables</li>
          <li>Khng commit .env files vo git</li>
          <li>S dng different .env files cho different environments</li>
          <li>Validate required variables khi app start</li>
          <li>Khng store sensitive secrets trong client-side code</li>
        </ul>
      </div>
    </div>
  );
}

export default EnvironmentVariables;

