import { useState } from 'react';

function ListsAndKeys() {
  const [items, setItems] = useState([
    { id: 1, name: 'Item 1', category: 'A' },
    { id: 2, name: 'Item 2', category: 'B' },
    { id: 3, name: 'Item 3', category: 'A' }
  ]);
  const [newItemName, setNewItemName] = useState('');

  const addItem = () => {
    if (newItemName.trim()) {
      setItems([...items, {
        id: Date.now(),
        name: newItemName,
        category: 'A'
      }]);
      setNewItemName('');
    }
  };

  const removeItem = (id) => {
    setItems(items.filter(item => item.id !== id));
  };

  return (
    <div className="topic-section">
      <h2>Lists and Keys</h2>
      
      <div className="definition-box">
        <h4>nh ngha</h4>
        <p>
          Trong React, bn thng cn render danh sch cc phn t.  render mt mng 
          cc phn t, bn s dng phng thc <span className="highlight">map()</span>. 
          Mi phn t trong danh sch cn c mt <span className="highlight">key</span> 
          duy nht  React c th theo di v cp nht hiu qu.
        </p>
      </div>

      <h3>Rendering Lists vi map()</h3>
      <p>
        S dng phng thc <span className="highlight">map()</span>  chuyn i 
        mng d liu thnh mng cc React elements.
      </p>

      <div className="code-example">
        <pre>{`const numbers = [1, 2, 3, 4, 5];

function NumberList() {
  const listItems = numbers.map((number) => (
    <li key={number.toString()}>{number}</li>
  ));
  
  return <ul>{listItems}</ul>;
}

// Hoc inline
function NumberList() {
  return (
    <ul>
      {numbers.map((number) => (
        <li key={number.toString()}>{number}</li>
      ))}
    </ul>
  );
}`}</pre>
      </div>

      <h3>Keys trong React</h3>
      <p>
        <strong>Keys</strong> gip React xc nh phn t no  thay i, c thm, 
        hoc b xa. Keys phi l unique trong cng mt danh sch.
      </p>

      <div className="code-example">
        <pre>{`//  NG - S dng unique ID
const todos = [
  { id: 1, text: 'Learn React' },
  { id: 2, text: 'Build app' }
];

function TodoList() {
  return (
    <ul>
      {todos.map((todo) => (
        <li key={todo.id}>{todo.text}</li>
      ))}
    </ul>
  );
}

//  SAI - Khng c key
{todos.map((todo) => (
  <li>{todo.text}</li> //  Warning: Each child should have a key
))}

//  TRNH - S dng index lm key (ch khi danh sch khng thay i)
{todos.map((todo, index) => (
  <li key={index}>{todo.text}</li> //  Khng khuyn ngh
))}`}</pre>
      </div>

      <div className="warning-box">
        <strong>Quan trng v Keys:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li>Keys phi unique trong cng mt danh sch</li>
          <li>Keys ch cn unique trong cng mt level, khng cn global unique</li>
          <li>Khng s dng index lm key nu danh sch c th thay i (thm/xa/sp xp)</li>
          <li>Keys khng c truyn nh props, chng ch dng cho React internal</li>
        </ul>
      </div>

      <div className="interactive-demo">
        <h4>V d: Todo List vi Keys</h4>
        <div style={{ marginBottom: '15px' }}>
          <input
            type="text"
            value={newItemName}
            onChange={(e) => setNewItemName(e.target.value)}
            onKeyPress={(e) => e.key === 'Enter' && addItem()}
            placeholder="Thm item mi..."
            style={{
              padding: '8px',
              marginRight: '10px',
              borderRadius: '5px',
              border: '1px solid #667eea',
              width: '200px'
            }}
          />
          <button
            onClick={addItem}
            style={{
              padding: '8px 15px',
              background: '#4caf50',
              color: 'white',
              border: 'none',
              borderRadius: '5px',
              cursor: 'pointer'
            }}
          >
            Thm
          </button>
        </div>
        <ul style={{ listStyle: 'none', padding: 0 }}>
          {items.map((item) => (
            <li
              key={item.id}
              style={{
                padding: '10px',
                margin: '5px 0',
                background: '#f5f5f5',
                borderRadius: '5px',
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center'
              }}
            >
              <span>{item.name} (ID: {item.id})</span>
              <button
                onClick={() => removeItem(item.id)}
                style={{
                  padding: '5px 10px',
                  background: '#f44336',
                  color: 'white',
                  border: 'none',
                  borderRadius: '3px',
                  cursor: 'pointer'
                }}
              >
                Xa
              </button>
            </li>
          ))}
        </ul>
      </div>

      <h3>Extracting Components vi Keys</h3>
      <p>
        Khi render danh sch components, key nn c t  component trong danh sch, 
        khng phi  component con.
      </p>

      <div className="code-example">
        <pre>{`//  SAI - Key  component con
function ListItem({ item }) {
  return <li key={item.id}>{item.text}</li>; //  Key khng hot ng  y
}

function TodoList() {
  return (
    <ul>
      {todos.map((todo) => (
        <ListItem item={todo} /> //  Thiu key
      ))}
    </ul>
  );
}

//  NG - Key  component trong danh sch
function ListItem({ item }) {
  return <li>{item.text}</li>;
}

function TodoList() {
  return (
    <ul>
      {todos.map((todo) => (
        <ListItem key={todo.id} item={todo} /> //  Key  y
      ))}
    </ul>
  );
}`}</pre>
      </div>

      <h3>Keys ch cn unique trong cng mt level</h3>
      <p>
        Keys ch cn unique trong cng mt danh sch, khng cn unique globally.
      </p>

      <div className="code-example">
        <pre>{`function Blog() {
  const posts = [
    { id: 1, title: 'Post 1' },
    { id: 2, title: 'Post 2' }
  ];
  
  const comments = [
    { id: 1, text: 'Comment 1' },
    { id: 2, text: 'Comment 2' }
  ];
  
  return (
    <div>
      <section>
        {posts.map(post => (
          <article key={post.id}>{post.title}</article>
        ))}
      </section>
      <section>
        {comments.map(comment => (
          <div key={comment.id}>{comment.text}</div>
        ))}
      </section>
    </div>
  );
}

// C th s dng cng ID trong posts v comments
// v chng  cc danh sch khc nhau`}</pre>
      </div>

      <h3>Filtering v Transforming Lists</h3>
      <p>
        Bn c th kt hp map() vi filter() v cc phng thc mng khc.
      </p>

      <div className="code-example">
        <pre>{`function TodoList({ todos }) {
  // Filter completed todos
  const completedTodos = todos.filter(todo => todo.completed);
  
  // Map v filter kt hp
  const activeTodos = todos
    .filter(todo => !todo.completed)
    .map(todo => (
      <li key={todo.id}>{todo.text}</li>
    ));
  
  return (
    <div>
      <h3>Active Todos</h3>
      <ul>{activeTodos}</ul>
      
      <h3>Completed: {completedTodos.length}</h3>
    </div>
  );
}`}</pre>
      </div>

      <h3>Nested Lists</h3>
      <p>
        Khi render nested lists, mi level cn c keys ring.
      </p>

      <div className="code-example">
        <pre>{`function CategoryList({ categories }) {
  return (
    <ul>
      {categories.map(category => (
        <li key={category.id}>
          <h3>{category.name}</h3>
          <ul>
            {category.items.map(item => (
              <li key={item.id}>{item.name}</li>
            ))}
          </ul>
        </li>
      ))}
    </ul>
  );
}`}</pre>
      </div>

      <h3>Conditional Rendering trong Lists</h3>
      <p>
        Bn c th kt hp conditional rendering vi lists.
      </p>

      <div className="code-example">
        <pre>{`function TodoList({ todos }) {
  if (todos.length === 0) {
    return <p>No todos yet!</p>;
  }
  
  return (
    <ul>
      {todos.map(todo => (
        <li key={todo.id}>
          {todo.completed ? (
            <s>{todo.text}</s>
          ) : (
            <span>{todo.text}</span>
          )}
        </li>
      ))}
    </ul>
  );
}`}</pre>
      </div>

      <h3>Performance vi Large Lists</h3>
      <p>
        Vi danh sch ln, React c th chm. Cn nhc s dng virtualization 
        (react-window, react-virtualized) hoc pagination.
      </p>

      <div className="code-example">
        <pre>{`// Vi danh sch ln, c th s dng pagination
function LargeList({ items }) {
  const [page, setPage] = useState(0);
  const itemsPerPage = 10;
  const startIndex = page * itemsPerPage;
  const endIndex = startIndex + itemsPerPage;
  const currentItems = items.slice(startIndex, endIndex);
  
  return (
    <div>
      <ul>
        {currentItems.map(item => (
          <li key={item.id}>{item.name}</li>
        ))}
      </ul>
      <button onClick={() => setPage(page - 1)} disabled={page === 0}>
        Previous
      </button>
      <button onClick={() => setPage(page + 1)} disabled={endIndex >= items.length}>
        Next
      </button>
    </div>
  );
}`}</pre>
      </div>

      <div className="info-box">
        <strong>Best Practices:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li>Lun s dng keys khi render lists</li>
          <li>S dng stable, unique IDs lm keys (t database, UUID, etc.)</li>
          <li>Trnh s dng index lm key tr khi danh sch khng bao gi thay i</li>
          <li>t key  element trong map(), khng phi component con</li>
          <li>Tch logic phc tp thnh helper functions</li>
          <li>Cn nhc performance cho danh sch ln</li>
        </ul>
      </div>
    </div>
  );
}

export default ListsAndKeys;

