import { useState } from 'react';

// Example components
function UserCard({ name, age, email, isActive }) {
  return (
    <div style={{
      border: '2px solid #667eea',
      borderRadius: '8px',
      padding: '15px',
      margin: '10px 0',
      background: isActive ? '#e8f5e9' : '#f5f5f5'
    }}>
      <h3>{name}</h3>
      <p>Tui: {age}</p>
      <p>Email: {email}</p>
      <p>Trng thi: {isActive ? 'Hot ng' : 'Khng hot ng'}</p>
    </div>
  );
}

function Button({ text, onClick, variant = 'primary' }) {
  const styles = {
    primary: { background: '#667eea', color: 'white' },
    secondary: { background: '#6c757d', color: 'white' },
    danger: { background: '#dc3545', color: 'white' }
  };

  return (
    <button
      onClick={onClick}
      style={{
        padding: '10px 20px',
        border: 'none',
        borderRadius: '5px',
        cursor: 'pointer',
        ...styles[variant]
      }}
    >
      {text}
    </button>
  );
}

function Props() {
  const [count, setCount] = useState(0);

  return (
    <div className="topic-section">
      <h2>Props</h2>
      
      <div className="definition-box">
        <h4>nh ngha</h4>
        <p>
          <strong>Props (Properties)</strong> l cch  truyn d liu t component cha 
          xung component con. Props l read-only (ch c), component con khng th thay i 
          props m n nhn c. Props gip components tr nn linh hot v c th ti s dng.
        </p>
      </div>

      <h3>Cch truyn Props</h3>
      <p>
        Props c truyn ging nh cc thuc tnh HTML. Trong component con, 
        props c nhn nh mt object trong tham s ca hm.
      </p>

      <div className="code-example">
        <pre>{`// Component cha truyn props
function App() {
  return <Greeting name="React" age={5} />;
}

// Component con nhn props
function Greeting({ name, age }) {
  return (
    <div>
      <h1>Hello, {name}!</h1>
      <p>Age: {age}</p>
    </div>
  );
}

// Hoc nhn ton b props object
function Greeting(props) {
  return (
    <div>
      <h1>Hello, {props.name}!</h1>
      <p>Age: {props.age}</p>
    </div>
  );
}`}</pre>
      </div>

      <h3>Props l Read-Only</h3>
      <div className="warning-box">
        <strong>Quan trng:</strong> Props l immutable (khng th thay i). 
        Component con khng c thay i props. Nu cn thay i d liu, 
        bn phi s dng state.
      </div>

      <div className="code-example">
        <pre>{`//  SAI - Khng c thay i props
function BadComponent({ count }) {
  count = count + 1; //  Li!
  return <div>{count}</div>;
}

//  NG - S dng state nu cn thay i
function GoodComponent({ initialCount }) {
  const [count, setCount] = useState(initialCount);
  return (
    <div>
      <p>{count}</p>
      <button onClick={() => setCount(count + 1)}>Increment</button>
    </div>
  );
}`}</pre>
      </div>

      <h3>Default Props</h3>
      <p>
        Bn c th t gi tr mc nh cho props bng cch s dng default parameters 
        hoc destructuring vi gi tr mc nh.
      </p>

      <div className="code-example">
        <pre>{`// S dng default parameters
function Button({ text = 'Click me', onClick }) {
  return <button onClick={onClick}>{text}</button>;
}

// S dng destructuring vi gi tr mc nh
function UserCard({ name = 'Anonymous', age = 0 }) {
  return (
    <div>
      <h3>{name}</h3>
      <p>Age: {age}</p>
    </div>
  );
}

// S dng
<Button /> // text s l "Click me"
<UserCard /> // name s l "Anonymous", age s l 0`}</pre>
      </div>

      <div className="interactive-demo">
        <h4>V d tng tc vi Props</h4>
        <div style={{ marginBottom: '20px' }}>
          <UserCard 
            name="Nguyn Vn A" 
            age={25} 
            email="nguyenvana@example.com"
            isActive={true}
          />
          <UserCard 
            name="Trn Th B" 
            age={30} 
            email="tranthib@example.com"
            isActive={false}
          />
        </div>
        
        <div style={{ display: 'flex', gap: '10px', alignItems: 'center' }}>
          <Button 
            text={`m: ${count}`}
            onClick={() => setCount(count + 1)}
            variant="primary"
          />
          <Button 
            text="Reset"
            onClick={() => setCount(0)}
            variant="secondary"
          />
          <Button 
            text="Gim"
            onClick={() => setCount(count - 1)}
            variant="danger"
          />
        </div>
      </div>

      <h3>Truyn Functions nh Props</h3>
      <p>
        Bn c th truyn functions nh props  component con c th giao tip 
        vi component cha (callback pattern).
      </p>

      <div className="code-example">
        <pre>{`function Parent() {
  const handleClick = () => {
    alert('Button clicked!');
  };

  return <Child onButtonClick={handleClick} />;
}

function Child({ onButtonClick }) {
  return <button onClick={onButtonClick}>Click me</button>;
}`}</pre>
      </div>

      <h3>Children Prop</h3>
      <p>
        <span className="highlight">children</span> l mt prop c bit cha ni dung 
        gia th m v th ng ca component.
      </p>

      <div className="code-example">
        <pre>{`function Container({ children }) {
  return (
    <div style={{ padding: '20px', border: '1px solid #ccc' }}>
      {children}
    </div>
  );
}

// S dng
<Container>
  <h1>Title</h1>
  <p>Content</p>
</Container>

// children s cha <h1> v <p>`}</pre>
      </div>

      <h3>PropTypes (Type Checking)</h3>
      <p>
         m bo props c ng kiu d liu, bn c th s dng PropTypes 
        (hoc TypeScript cho type safety tt hn).
      </p>

      <div className="code-example">
        <pre>{`import PropTypes from 'prop-types';

function UserCard({ name, age, email }) {
  return (
    <div>
      <h3>{name}</h3>
      <p>Age: {age}</p>
      <p>Email: {email}</p>
    </div>
  );
}

UserCard.propTypes = {
  name: PropTypes.string.isRequired,
  age: PropTypes.number.isRequired,
  email: PropTypes.string.isRequired
};`}</pre>
      </div>

      <div className="info-box">
        <strong>Best Practices:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li>t tn props r rng, m t mc ch</li>
          <li>S dng destructuring  nhn props</li>
          <li>t gi tr mc nh cho props optional</li>
          <li>Khng thay i props trong component con</li>
          <li>S dng PropTypes hoc TypeScript  kim tra kiu d liu</li>
        </ul>
      </div>
    </div>
  );
}

export default Props;

