import { useState } from 'react';

function EventHandling() {
  const [message, setMessage] = useState('');
  const [count, setCount] = useState(0);
  const [formData, setFormData] = useState({ name: '', email: '' });

  const handleClick = () => {
    setMessage('Button  c click!');
  };

  const handleInputChange = (e) => {
    setMessage(e.target.value);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    alert(`Form submitted: ${formData.name} - ${formData.email}`);
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      alert('Enter key pressed!');
    }
  };

  return (
    <div className="topic-section">
      <h2>Event Handling</h2>
      
      <div className="definition-box">
        <h4>nh ngha</h4>
        <p>
          <strong>Event Handling</strong> trong React cho php bn x l cc s kin 
          tng tc ca ngi dng nh click, input, submit, v.v. React s dng 
          SyntheticEvent - mt wrapper xung quanh native browser events  m bo 
          tnh nht qun gia cc trnh duyt.
        </p>
      </div>

      <h3>C php c bn</h3>
      <p>
        Trong React, event handlers c vit vi camelCase v nhn mt SyntheticEvent 
        lm tham s.
      </p>

      <div className="code-example">
        <pre>{`function Button() {
  const handleClick = () => {
    console.log('Button clicked!');
  };
  
  return <button onClick={handleClick}>Click me</button>;
}

// Hoc inline
function Button() {
  return (
    <button onClick={() => console.log('Clicked!')}>
      Click me
    </button>
  );
}`}</pre>
      </div>

      <h3>Cc loi Events ph bin</h3>
      <div className="code-example">
        <pre>{`function Component() {
  const handleClick = (e) => console.log('Click', e);
  const handleChange = (e) => console.log('Change', e.target.value);
  const handleSubmit = (e) => {
    e.preventDefault();
    console.log('Submit');
  };
  const handleMouseOver = (e) => console.log('Mouse over');
  const handleFocus = (e) => console.log('Focus');
  const handleBlur = (e) => console.log('Blur');
  
  return (
    <form onSubmit={handleSubmit}>
      <input
        onChange={handleChange}
        onFocus={handleFocus}
        onBlur={handleBlur}
        onMouseOver={handleMouseOver}
      />
      <button onClick={handleClick}>Click</button>
    </form>
  );
}`}</pre>
      </div>

      <div className="interactive-demo">
        <h4>V d tng tc: Click Event</h4>
        <button 
          onClick={handleClick}
          style={{
            padding: '10px 20px',
            background: '#667eea',
            color: 'white',
            border: 'none',
            borderRadius: '5px',
            cursor: 'pointer',
            marginBottom: '15px'
          }}
        >
          Click me
        </button>
        {message && <p style={{ color: '#667eea' }}>{message}</p>}
      </div>

      <h3>Event Object (SyntheticEvent)</h3>
      <p>
        React cung cp SyntheticEvent - mt wrapper xung quanh native event. 
        N c interface ging native event nhng hot ng nht qun trn mi trnh duyt.
      </p>

      <div className="code-example">
        <pre>{`function Component() {
  const handleClick = (e) => {
    e.preventDefault(); // Ngn hnh vi mc nh
    e.stopPropagation(); // Ngn event bubbling
    
    console.log(e.target); // Element gy ra event
    console.log(e.currentTarget); // Element c event handler
    console.log(e.type); // Loi event ('click', 'change', etc.)
  };
  
  return <button onClick={handleClick}>Click</button>;
}`}</pre>
      </div>

      <h3>Passing Arguments to Event Handlers</h3>
      <p>
        Bn c th truyn thm tham s cho event handler bng cch s dng arrow function 
        hoc bind.
      </p>

      <div className="code-example">
        <pre>{`function TodoList() {
  const todos = ['Task 1', 'Task 2', 'Task 3'];
  
  // Cch 1: Arrow function
  const handleDelete = (id) => {
    console.log('Delete', id);
  };
  
  return (
    <ul>
      {todos.map((todo, index) => (
        <li key={index}>
          {todo}
          <button onClick={() => handleDelete(index)}>
            Delete
          </button>
        </li>
      ))}
    </ul>
  );
}

// Cch 2: Bind
function Component() {
  const handleClick = function(id, e) {
    console.log('ID:', id, 'Event:', e);
  };
  
  return <button onClick={handleClick.bind(null, 123)}>Click</button>;
}`}</pre>
      </div>

      <div className="interactive-demo">
        <h4>V d: Input Event</h4>
        <input
          type="text"
          placeholder="Nhp text..."
          onChange={handleInputChange}
          onKeyPress={handleKeyPress}
          style={{
            padding: '8px',
            width: '300px',
            borderRadius: '5px',
            border: '1px solid #667eea',
            marginBottom: '10px'
          }}
        />
        <p>Bn  nhp: <strong>{message}</strong></p>
        <p style={{ fontSize: '0.9rem', color: '#666' }}>
          (Th nhn Enter  xem keyPress event)
        </p>
      </div>

      <h3>Form Events</h3>
      <p>
        X l form events i hi ngn hnh vi mc nh ca browser v qun l state 
        ca form inputs.
      </p>

      <div className="code-example">
        <pre>{`function Form() {
  const [formData, setFormData] = useState({ name: '', email: '' });
  
  const handleSubmit = (e) => {
    e.preventDefault(); // Ngn form submit mc nh
    console.log('Form data:', formData);
    // X l submit logic
  };
  
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };
  
  return (
    <form onSubmit={handleSubmit}>
      <input
        name="name"
        value={formData.name}
        onChange={handleChange}
      />
      <input
        name="email"
        value={formData.email}
        onChange={handleChange}
      />
      <button type="submit">Submit</button>
    </form>
  );
}`}</pre>
      </div>

      <div className="interactive-demo">
        <h4>V d: Form vi Submit Event</h4>
        <form onSubmit={handleSubmit} style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>
          <input
            name="name"
            placeholder="Tn"
            value={formData.name}
            onChange={(e) => setFormData({ ...formData, name: e.target.value })}
            style={{ padding: '8px', borderRadius: '5px', border: '1px solid #667eea' }}
          />
          <input
            name="email"
            placeholder="Email"
            value={formData.email}
            onChange={(e) => setFormData({ ...formData, email: e.target.value })}
            style={{ padding: '8px', borderRadius: '5px', border: '1px solid #667eea' }}
          />
          <button 
            type="submit"
            style={{
              padding: '10px',
              background: '#667eea',
              color: 'white',
              border: 'none',
              borderRadius: '5px',
              cursor: 'pointer'
            }}
          >
            Submit
          </button>
        </form>
      </div>

      <h3>Event Bubbling v stopPropagation</h3>
      <p>
        Events trong React bubble ln theo DOM tree. Bn c th ngn bubbling bng 
        <span className="highlight"> stopPropagation()</span>.
      </p>

      <div className="code-example">
        <pre>{`function Parent() {
  const handleParentClick = () => {
    console.log('Parent clicked');
  };
  
  return (
    <div onClick={handleParentClick} style={{ padding: '20px', background: '#f0f0f0' }}>
      <Child />
    </div>
  );
}

function Child() {
  const handleChildClick = (e) => {
    e.stopPropagation(); // Ngn event bubble ln parent
    console.log('Child clicked');
  };
  
  return (
    <button onClick={handleChildClick}>
      Click me
    </button>
  );
}`}</pre>
      </div>

      <h3>Conditional Event Handlers</h3>
      <p>
        Bn c th iu kin ha event handlers da trn state hoc props.
      </p>

      <div className="code-example">
        <pre>{`function Button({ disabled }) {
  const handleClick = () => {
    if (disabled) return; // Khng lm g nu disabled
    console.log('Clicked');
  };
  
  return (
    <button 
      onClick={handleClick}
      disabled={disabled}
    >
      Click
    </button>
  );
}`}</pre>
      </div>

      <div className="interactive-demo">
        <h4>V d: Counter vi nhiu events</h4>
        <div style={{ textAlign: 'center' }}>
          <h2 style={{ color: '#667eea' }}>{count}</h2>
          <div style={{ display: 'flex', gap: '10px', justifyContent: 'center' }}>
            <button
              onClick={() => setCount(count + 1)}
              style={{
                padding: '10px 20px',
                background: '#4caf50',
                color: 'white',
                border: 'none',
                borderRadius: '5px',
                cursor: 'pointer'
              }}
            >
              Tng
            </button>
            <button
              onClick={() => setCount(count - 1)}
              style={{
                padding: '10px 20px',
                background: '#f44336',
                color: 'white',
                border: 'none',
                borderRadius: '5px',
                cursor: 'pointer'
              }}
            >
              Gim
            </button>
            <button
              onClick={() => setCount(0)}
              style={{
                padding: '10px 20px',
                background: '#ff9800',
                color: 'white',
                border: 'none',
                borderRadius: '5px',
                cursor: 'pointer'
              }}
            >
              Reset
            </button>
          </div>
        </div>
      </div>

      <h3>Custom Events</h3>
      <p>
        Bn c th to custom event handlers  truyn data t component con ln component cha.
      </p>

      <div className="code-example">
        <pre>{`function Parent() {
  const handleCustomEvent = (data) => {
    console.log('Received:', data);
  };
  
  return <Child onCustomEvent={handleCustomEvent} />;
}

function Child({ onCustomEvent }) {
  const handleClick = () => {
    onCustomEvent({ message: 'Hello from child!' });
  };
  
  return <button onClick={handleClick}>Send Data</button>;
}`}</pre>
      </div>

      <div className="warning-box">
        <strong>Lu  quan trng:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li>Event handlers phi l functions, khng phi function calls</li>
          <li>S dng arrow functions hoc bind  truyn tham s</li>
          <li>Lun preventDefault() cho form submit nu x l th cng</li>
          <li>SyntheticEvent c pool, nn khng th truy cp sau khi event handler kt thc</li>
          <li>Nu cn truy cp event sau , gi e.persist()</li>
        </ul>
      </div>

      <div className="info-box">
        <strong>Best Practices:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li>t tn event handlers r rng (handleClick, handleSubmit, etc.)</li>
          <li>Trnh inline functions phc tp, tch ra thnh functions ring</li>
          <li>S dng preventDefault() cho form submissions</li>
          <li>X l errors trong event handlers</li>
          <li>Trnh mutate state trc tip trong event handlers</li>
        </ul>
      </div>
    </div>
  );
}

export default EventHandling;

