function JSONDataStructures() {
  return (
    <div className="topic-section">
      <h2>JSON Data Structures</h2>
      
      <div className="definition-box">
        <h4>nh ngha</h4>
        <p>
          <strong>JSON (JavaScript Object Notation)</strong> l mt nh dng d liu nh, 
          d c v d parse. JSON c s dng rng ri  trao i d liu gia client v server. 
          JSON ch h tr cc kiu d liu c bn: strings, numbers, booleans, null, objects, v arrays.
        </p>
      </div>

      <h3>Cu trc JSON c bn</h3>
      <div className="code-example">
        <pre>{`// Object JSON
{
  "name": "John Doe",
  "age": 30,
  "isActive": true,
  "email": "john@example.com"
}

// Array JSON
[
  { "id": 1, "name": "Item 1" },
  { "id": 2, "name": "Item 2" },
  { "id": 3, "name": "Item 3" }
]

// Nested structures
{
  "user": {
    "id": 123,
    "name": "John",
    "address": {
      "street": "123 Main St",
      "city": "New York"
    }
  },
  "posts": [
    { "id": 1, "title": "Post 1" },
    { "id": 2, "title": "Post 2" }
  ]
}`}</pre>
      </div>

      <h3>JSON.parse() v JSON.stringify()</h3>
      <p>
        JavaScript cung cp 2 methods chnh  lm vic vi JSON:
      </p>

      <div className="code-example">
        <pre>{`// JSON.stringify() - Chuyn JavaScript object thnh JSON string
const user = {
  name: 'John',
  age: 30,
  isActive: true
};

const jsonString = JSON.stringify(user);
console.log(jsonString);
// Output: {"name":"John","age":30,"isActive":true}

// JSON.parse() - Chuyn JSON string thnh JavaScript object
const jsonString = '{"name":"John","age":30,"isActive":true}';
const user = JSON.parse(jsonString);
console.log(user.name); // Output: John`}</pre>
      </div>

      <h3>Lm vic vi JSON trong React</h3>
      <div className="code-example">
        <pre>{`// Fetch v parse JSON
async function fetchUsers() {
  const response = await fetch('/api/users');
  const users = await response.json(); // T ng parse JSON
  return users;
}

// Gi JSON data
async function createUser(userData) {
  const response = await fetch('/api/users', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json'
    },
    body: JSON.stringify(userData) // Chuyn object thnh JSON string
  });
  return await response.json();
}

// S dng trong component
function UserList() {
  const [users, setUsers] = useState([]);
  
  useEffect(() => {
    async function loadUsers() {
      const data = await fetchUsers();
      setUsers(data);
    }
    loadUsers();
  }, []);
  
  return (
    <div>
      {users.map(user => (
        <div key={user.id}>{user.name}</div>
      ))}
    </div>
  );
}`}</pre>
      </div>

      <h3>X l nested JSON</h3>
      <p>
        Khi lm vic vi nested structures, s dng optional chaining v nullish coalescing:
      </p>

      <div className="code-example">
        <pre>{`// JSON response t API
const response = {
  "data": {
    "user": {
      "profile": {
        "name": "John",
        "email": "john@example.com"
      }
    }
  }
};

//  C th gy li nu structure khng ng
const name = response.data.user.profile.name;

//  Safe vi optional chaining
const name = response?.data?.user?.profile?.name ?? 'Unknown';

//  Hoc vi destructuring
const { name = 'Unknown', email = '' } = response?.data?.user?.profile ?? {};`}</pre>
      </div>

      <h3>Validating JSON</h3>
      <p>
        Lun validate JSON data trc khi s dng:
      </p>

      <div className="code-example">
        <pre>{`function parseJSONSafely(jsonString) {
  try {
    const data = JSON.parse(jsonString);
    return { success: true, data };
  } catch (error) {
    console.error('Invalid JSON:', error);
    return { success: false, error: error.message };
  }
}

// S dng
const result = parseJSONSafely('{"name":"John"}');
if (result.success) {
  console.log(result.data);
} else {
  console.error('Parse failed:', result.error);
}`}</pre>
      </div>

      <h3>JSON Limitations</h3>
      <div className="warning-box">
        <strong>JSON khng h tr:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li>Functions - Khng th serialize functions</li>
          <li>Undefined - S b b qua khi stringify</li>
          <li>Dates - S c chuyn thnh strings</li>
          <li>Circular references - Gy li khi stringify</li>
          <li>Symbols - S b b qua</li>
        </ul>
      </div>

      <div className="code-example">
        <pre>{`//  Functions s b b qua
const obj = {
  name: 'John',
  greet: function() { return 'Hello'; }
};
JSON.stringify(obj); // {"name":"John"}

//  Dates s thnh string
const obj = { date: new Date() };
JSON.stringify(obj); // {"date":"2024-01-01T00:00:00.000Z"}

//  Cn convert dates manually
const obj = {
  date: new Date().toISOString()
};
JSON.stringify(obj); // {"date":"2024-01-01T00:00:00.000Z"}

//  Parse li dates
const parsed = JSON.parse(jsonString);
const date = new Date(parsed.date);`}</pre>
      </div>

      <h3>Common JSON Patterns</h3>
      <div className="code-example">
        <pre>{`// API Response wrapper
{
  "success": true,
  "data": {
    "users": [...]
  },
  "message": "Users fetched successfully"
}

// Error response
{
  "success": false,
  "error": {
    "code": "USER_NOT_FOUND",
    "message": "User not found"
  }
}

// Paginated response
{
  "data": [...],
  "pagination": {
    "page": 1,
    "perPage": 10,
    "total": 100,
    "totalPages": 10
  }
}`}</pre>
      </div>

      <div className="info-box">
        <strong>Best Practices:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li>Lun validate JSON trc khi parse</li>
          <li>S dng try/catch khi parse JSON</li>
          <li>Kim tra structure vi optional chaining</li>
          <li>X l dates v special types cn thn</li>
          <li>S dng consistent response format</li>
        </ul>
      </div>
    </div>
  );
}

export default JSONDataStructures;

