import { useState } from 'react';

// Example components for composition
function Card({ children, title }) {
  return (
    <div style={{
      border: '2px solid #667eea',
      borderRadius: '8px',
      padding: '20px',
      margin: '10px 0',
      background: 'white'
    }}>
      {title && <h3 style={{ color: '#667eea', marginBottom: '15px' }}>{title}</h3>}
      {children}
    </div>
  );
}

function Button({ children, onClick, variant = 'primary' }) {
  const styles = {
    primary: { background: '#667eea', color: 'white' },
    secondary: { background: '#6c757d', color: 'white' }
  };

  return (
    <button
      onClick={onClick}
      style={{
        padding: '10px 20px',
        border: 'none',
        borderRadius: '5px',
        cursor: 'pointer',
        ...styles[variant]
      }}
    >
      {children}
    </button>
  );
}

function Dialog({ isOpen, onClose, title, children }) {
  if (!isOpen) return null;

  return (
    <div style={{
      position: 'fixed',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      background: 'rgba(0,0,0,0.5)',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      zIndex: 1000
    }}>
      <div style={{
        background: 'white',
        padding: '20px',
        borderRadius: '8px',
        maxWidth: '500px',
        width: '90%'
      }}>
        <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: '15px' }}>
          <h3>{title}</h3>
          <button onClick={onClose}></button>
        </div>
        {children}
      </div>
    </div>
  );
}

function ComponentComposition() {
  const [dialogOpen, setDialogOpen] = useState(false);

  return (
    <div className="topic-section">
      <h2>Component Composition</h2>
      
      <div className="definition-box">
        <h4>nh ngha</h4>
        <p>
          <strong>Component Composition</strong> l k thut xy dng UI phc tp bng cch 
          kt hp cc components nh, n gin li vi nhau. Thay v to mt component ln 
          vi nhiu logic, bn chia nh thnh cc components c th ti s dng v kt hp 
          chng li. y l mt trong nhng nguyn tc ct li ca React.
        </p>
      </div>

      <h3>Containment vi children prop</h3>
      <p>
        Mt trong nhng patterns ph bin nht l s dng <span className="highlight">children</span> 
        prop  cho php component cha truyn ni dung ty  vo component con.
      </p>

      <div className="code-example">
        <pre>{`function Container({ children }) {
  return (
    <div style={{ padding: '20px', border: '1px solid #ccc' }}>
      {children}
    </div>
  );
}

function App() {
  return (
    <Container>
      <h1>Title</h1>
      <p>Content</p>
    </Container>
  );
}`}</pre>
      </div>

      <div className="interactive-demo">
        <h4>V d: Card Component vi children</h4>
        <Card title="Card vi children prop">
          <p>y l ni dung c truyn vo qua children prop.</p>
          <p>Bn c th truyn bt k JSX no vo y.</p>
          <Button onClick={() => alert('Clicked!')}>Click me</Button>
        </Card>
      </div>

      <h3>Specialization (c bit ha)</h3>
      <p>
        To cc components c bit t components tng qut bng cch s dng composition.
      </p>

      <div className="code-example">
        <pre>{`// Component tng qut
function Dialog({ title, children, isOpen, onClose }) {
  if (!isOpen) return null;
  
  return (
    <div className="dialog-overlay" onClick={onClose}>
      <div className="dialog">
        <h2>{title}</h2>
        {children}
      </div>
    </div>
  );
}

// Components c bit
function WelcomeDialog({ isOpen, onClose }) {
  return (
    <Dialog title="Welcome" isOpen={isOpen} onClose={onClose}>
      <p>Welcome to our app!</p>
    </Dialog>
  );
}

function ConfirmDialog({ isOpen, onClose, onConfirm, message }) {
  return (
    <Dialog title="Confirm" isOpen={isOpen} onClose={onClose}>
      <p>{message}</p>
      <button onClick={onConfirm}>Confirm</button>
      <button onClick={onClose}>Cancel</button>
    </Dialog>
  );
}`}</pre>
      </div>

      <div className="interactive-demo">
        <h4>V d: Dialog Component</h4>
        <Button onClick={() => setDialogOpen(true)}>M Dialog</Button>
        <Dialog
          isOpen={dialogOpen}
          onClose={() => setDialogOpen(false)}
          title="Dialog Example"
        >
          <p>y l mt dialog c to bng component composition.</p>
          <p>Bn c th ng n bng cch click vo nt X hoc click ra ngoi.</p>
        </Dialog>
      </div>

      <h3>Multiple Children Slots</h3>
      <p>
        Thay v ch c mt children, bn c th to nhiu "slots" bng cch s dng 
        props vi tn khc nhau.
      </p>

      <div className="code-example">
        <pre>{`function SplitPane({ left, right }) {
  return (
    <div className="split-pane">
      <div className="left-pane">{left}</div>
      <div className="right-pane">{right}</div>
    </div>
  );
}

function App() {
  return (
    <SplitPane
      left={<Contacts />}
      right={<Chat />}
    />
  );
}

// Hoc vi nhiu slots hn
function Layout({ header, sidebar, main, footer }) {
  return (
    <div className="layout">
      <header>{header}</header>
      <div className="body">
        <aside>{sidebar}</aside>
        <main>{main}</main>
      </div>
      <footer>{footer}</footer>
    </div>
  );
}`}</pre>
      </div>

      <h3>Composition vs Inheritance</h3>
      <p>
        React khuyn ngh s dng composition thay v inheritance. Thay v k tha, 
        bn nn compose components li vi nhau.
      </p>

      <div className="code-example">
        <pre>{`//  KHNG KHUYN NGH - Inheritance
class SpecialButton extends Button {
  render() {
    return <button className="special">{this.props.children}</button>;
  }
}

//  KHUYN NGH - Composition
function SpecialButton({ children, ...props }) {
  return (
    <Button className="special" {...props}>
      {children}
    </Button>
  );
}

// Hoc vi wrapper
function SpecialButton({ children }) {
  return (
    <div className="special-wrapper">
      <Button>{children}</Button>
    </div>
  );
}`}</pre>
      </div>

      <h3>Higher-Order Components (HOC) Pattern</h3>
      <p>
        HOC l mt function nhn component v tr v component mi vi thm functionality.
      </p>

      <div className="code-example">
        <pre>{`// HOC  thm loading state
function withLoading(Component) {
  return function WithLoadingComponent({ isLoading, ...props }) {
    if (isLoading) {
      return <div>Loading...</div>;
    }
    return <Component {...props} />;
  };
}

// S dng
const UserProfileWithLoading = withLoading(UserProfile);

function App() {
  return <UserProfileWithLoading isLoading={true} />;
}`}</pre>
      </div>

      <h3>Render Props Pattern</h3>
      <p>
        Render props l mt pattern ni component nhn mt function nh prop v gi n 
         render content.
      </p>

      <div className="code-example">
        <pre>{`function MouseTracker({ render }) {
  const [position, setPosition] = useState({ x: 0, y: 0 });
  
  useEffect(() => {
    const handleMouseMove = (e) => {
      setPosition({ x: e.clientX, y: e.clientY });
    };
    
    window.addEventListener('mousemove', handleMouseMove);
    return () => window.removeEventListener('mousemove', handleMouseMove);
  }, []);
  
  return render(position);
}

// S dng
function App() {
  return (
    <MouseTracker
      render={({ x, y }) => (
        <div>
          Mouse position: {x}, {y}
        </div>
      )}
    />
  );
}`}</pre>
      </div>

      <h3>Compound Components</h3>
      <p>
        Compound components l mt nhm components lm vic cng nhau  to mt UI phc tp.
      </p>

      <div className="code-example">
        <pre>{`// Compound components
function Tabs({ children }) {
  const [activeTab, setActiveTab] = useState(0);
  
  return (
    <div>
      {React.Children.map(children, (child, index) => {
        if (child.type === Tabs.Tab) {
          return React.cloneElement(child, {
            isActive: index === activeTab,
            onClick: () => setActiveTab(index)
          });
        }
        return child;
      })}
    </div>
  );
}

Tabs.Tab = function Tab({ isActive, onClick, children }) {
  return (
    <button
      onClick={onClick}
      style={{ fontWeight: isActive ? 'bold' : 'normal' }}
    >
      {children}
    </button>
  );
};

// S dng
function App() {
  return (
    <Tabs>
      <Tabs.Tab>Tab 1</Tabs.Tab>
      <Tabs.Tab>Tab 2</Tabs.Tab>
    </Tabs>
  );
}`}</pre>
      </div>

      <h3>Benefits ca Composition</h3>
      <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '20px', margin: '20px 0' }}>
        <div style={{ background: '#e8f5e9', padding: '15px', borderRadius: '8px' }}>
          <h4 style={{ color: '#2e7d32', marginBottom: '10px' }}>u im</h4>
          <ul style={{ paddingLeft: '20px', lineHeight: '1.8' }}>
            <li>Ti s dng code tt hn</li>
            <li>Components nh, d test</li>
            <li>Linh hot v d m rng</li>
            <li>D bo tr v debug</li>
            <li>Tun theo Single Responsibility Principle</li>
          </ul>
        </div>

        <div style={{ background: '#fff3e0', padding: '15px', borderRadius: '8px' }}>
          <h4 style={{ color: '#e65100', marginBottom: '10px' }}>Khi no dng</h4>
          <ul style={{ paddingLeft: '20px', lineHeight: '1.8' }}>
            <li>UI c nhiu phn lp li</li>
            <li>Cn ty bin ni dung</li>
            <li>Xy dng design system</li>
            <li>Components cn linh hot</li>
            <li>Trnh prop drilling</li>
          </ul>
        </div>
      </div>

      <h3>V d thc t: Layout System</h3>
      <div className="code-example">
        <pre>{`function PageLayout({ header, sidebar, content, footer }) {
  return (
    <div className="page-layout">
      <header>{header}</header>
      <div className="body">
        <aside>{sidebar}</aside>
        <main>{content}</main>
      </div>
      <footer>{footer}</footer>
    </div>
  );
}

function App() {
  return (
    <PageLayout
      header={<Header />}
      sidebar={<Sidebar />}
      content={<MainContent />}
      footer={<Footer />}
    />
  );
}`}</pre>
      </div>

      <div className="info-box">
        <strong>Best Practices:</strong>
        <ul style={{ marginTop: '10px', paddingLeft: '20px' }}>
          <li>u tin composition over inheritance</li>
          <li>Gi components nh v tp trung vo mt nhim v</li>
          <li>S dng children prop cho containment</li>
          <li>To components tng qut, sau  specialize</li>
          <li>Trnh prop drilling bng cch s dng Context khi cn</li>
          <li>Document props v usage patterns</li>
        </ul>
      </div>
    </div>
  );
}

export default ComponentComposition;

